import { TextElements, getDefaultChartText, getDefaultFont } from "@arcgis/charts-shared-utils";
import { getDescriptionText, getTitleText } from "./general";
/**
 * Gets the title text symbol from the chart config.
 * @param config
 */
export const getTitleSymbol = (config) => config.title?.content;
/**
 * Sets the title text symbol in the chart config.
 * @param config
 * @param newTitleSymbol
 */
export const setTitleSymbol = (newTitleSymbol, config) => {
    const prevText = getTitleText(config);
    if (config.title === undefined || newTitleSymbol === undefined) {
        config.title = getDefaultChartText({
            text: "",
            visible: true,
            font: getDefaultFont(TextElements.ChartTitle),
            type: TextElements.ChartTitle,
        });
    }
    if (newTitleSymbol !== undefined) {
        config.title.content = newTitleSymbol;
    }
    config.title.content.text = prevText;
};
