import { ChartModel } from "../chart-model/chart-model";
import { getColorMatch, setColorMatch } from "./utils/color-match";
export class ModelWithColorMatch extends ChartModel {
    /**
     * Gets whether the chart should attempt to match the data source's colors.
     *
     * `true`: Attempt to match the data source's colors.
     *
     * `false`: Use the colors defined in the config.
     *
     * @returns {boolean}
     */
    getColorMatch() {
        return getColorMatch(this._config);
    }
    /**
     * Sets whether the chart should attempt to match the data source's colors.
     *
     * `true`: Attempt to match the data source's colors.
     *
     * `false`: Use the colors defined in the config.
     *
     * @param {boolean} newColorMatch
     */
    setColorMatch(newColorMatch) {
        setColorMatch(newColorMatch, this._config);
        this.emitConfigUpdate("setColorMatch");
    }
}
