import { WebChartOrderDataByTypes, WebChartSortOrderKinds, WebChartTypes } from "@arcgis/charts-spec";
import { getChartOrSeriesType } from "@arcgis/charts-shared-utils";
import { SerialChartDataSortingKinds } from "../interfaces/common";
/**
 * Get the sort order kind for the bar and line chart
 * @param {ChartConfig<WebChartBarChartSeries | WebChartLineChartSeries>} config - the chart configuration
 *
 * @returns {SerialChartDataSortingKinds}
 */
export const getSortOrderKindForBarLineChart = (config) => {
    // x-axis ascending sort order by default
    let sortOrderKind = SerialChartDataSortingKinds.xAxisAsc;
    // extract sorting field and the order
    const orderType = config.orderOptions?.data?.orderType ?? WebChartOrderDataByTypes.Category;
    const order = config.orderOptions?.data?.orderBy ?? WebChartSortOrderKinds.Ascending;
    if (orderType === WebChartOrderDataByTypes.Category && Array.isArray(order)) {
        sortOrderKind = SerialChartDataSortingKinds.customSort;
    }
    else if (orderType === WebChartOrderDataByTypes.Category) {
        if (order === WebChartSortOrderKinds.Ascending) {
            sortOrderKind = SerialChartDataSortingKinds.xAxisAsc;
        }
        else {
            sortOrderKind = SerialChartDataSortingKinds.xAxisDesc;
        }
    }
    else if (order === WebChartSortOrderKinds.Ascending) {
        sortOrderKind = SerialChartDataSortingKinds.yAxisAsc;
    }
    else {
        sortOrderKind = SerialChartDataSortingKinds.yAxisDesc;
    }
    return sortOrderKind;
};
/**
 * Check if custom sort is enabled based on the chart type.
 * @param {ChartConfig<WebChartBarChartSeries | WebChartBoxPlotSeries | WebChartLineChartSeries | WebChartPieChartSeries>} config - The chart config.
 * @returns {boolean} - True if custom sort is enabled, otherwise false.
 */
export const isCustomSortEnabled = (config) => {
    const chartType = getChartOrSeriesType(config.series);
    let isEnabled = false;
    // check for Bar or Line chart
    if (chartType === WebChartTypes.BarSeries || chartType === WebChartTypes.LineSeries) {
        isEnabled =
            getSortOrderKindForBarLineChart(config) ===
                SerialChartDataSortingKinds.customSort;
    }
    // check for Box Plot chart
    // TODO: Uncomment this block when the box plot model is implemented for custom sort
    //   else if (chartType === WebChartTypes.BoxPlotSeries) {
    //     isEnabled =
    //       getSortOrderKindForBoxPlot(config as ChartConfig<WebChartBoxPlotSeries>) === BoxPlotDataSortingKinds.customSort;
    //   }
    // check for the pie chart
    // TODO: Uncomment this block when the pie chart model is implemented for custom sort
    //   else if (chartType === WebChartTypes.PieSeries) {
    //     const chartSubType = getChartSubType(config.series);
    //     const mode = chartSubType !== ChartSubType.PieFromFields ? PieChartDataModes.Category : PieChartDataModes.Fields;
    //     isEnabled =
    //       getSortOrderKindForPieChart(config as ChartConfig<WebChartPieChartSeries>, mode) ===
    //       PieChartDataSortingKinds.customSort;
    //   }
    return isEnabled;
};
/**
 * Get the order by list from the config.
 * The order by list is only used when the sort order kind is custom sort.
 * @param  {ChartConfig<WebChartBarChartSeries | WebChartBoxPlotSeries | WebChartLineChartSeries | WebChartPieChartSeries>} config - The chart config.
 *
 * @returns string[] - The order by list.
 */
export const getOrderByList = (config) => {
    let orderByList = [];
    const orderType = config.orderOptions?.data?.orderType;
    // If custom sort is enabled and order type is Category, retrieve the order by list
    if (isCustomSortEnabled(config) && orderType === WebChartOrderDataByTypes.Category) {
        orderByList = config.orderOptions?.data?.orderBy;
    }
    return orderByList;
};
/**
 * Set the sort order kind for the bar and line chart
 * @param {SerialChartDataSortingKinds} newSortOrderKind - the new sort order kind
 * @param {ChartConfig} config - the chart configuration
 * @param {string[] | undefined} orderByList - the list of fields to order by
 */
export const setSortOrderKind = (newSortOrderKind, config, orderByList) => {
    let orderType = WebChartOrderDataByTypes.Category;
    let orderBy = WebChartSortOrderKinds.Ascending;
    const chartType = getChartOrSeriesType(config.series);
    config.orderOptions ??= {};
    // update the orderOptions when custom sort is selected
    if (newSortOrderKind === SerialChartDataSortingKinds.customSort && orderByList !== undefined) {
        orderBy = [...orderByList];
        config.orderOptions.data = {
            orderType,
            orderBy,
        };
    }
    // update the orderOptions for the bar, line chart, and pie chart
    else if (chartType === WebChartTypes.BarSeries || chartType === WebChartTypes.LineSeries) {
        orderType =
            newSortOrderKind === SerialChartDataSortingKinds.xAxisAsc ||
                newSortOrderKind === SerialChartDataSortingKinds.xAxisDesc
                ? WebChartOrderDataByTypes.Category
                : WebChartOrderDataByTypes.YValue;
        orderBy =
            newSortOrderKind === SerialChartDataSortingKinds.xAxisAsc ||
                newSortOrderKind === SerialChartDataSortingKinds.yAxisAsc
                ? WebChartSortOrderKinds.Ascending
                : WebChartSortOrderKinds.Descending;
        config.orderOptions.data = {
            orderType,
            orderBy,
        };
    }
};
/**
 * Gets the x-axis field.
 * @param {ChartConfig} config - The chart config.
 * @returns {string}
 */
export const getXAxisField = (config) => config?.series?.[0]?.x ?? "";
