import { TextElements, getDefaultChartText, getDefaultFont } from "@arcgis/charts-shared-utils";
/**
 * Get the title text of the chart from the config
 * @param config - the config object
 */
export const getTitleText = (config) => config.title?.content.text ?? "";
/**
 * Set the title text of the chart
 * @param newTitleText - the new title text
 * @param config - the config object
 */
export const setTitleText = (newTitleText, config) => {
    if (config.title !== undefined) {
        config.title.content.text = newTitleText;
    }
    else if (config.title === undefined && newTitleText !== undefined) {
        config.title = getDefaultChartText({
            text: newTitleText,
            visible: true,
            font: getDefaultFont(TextElements.ChartTitle),
        });
    }
};
/**
 * Get the description text
 * @param config - the config object
 */
export const getDescriptionText = (config) => config.footer?.content.text ?? "";
/**
 * Set the description text of the chart
 * @param newDescriptionText  - the new description text
 * @param config - the config object
 */
export const setDescriptionText = (newDescriptionText, config) => {
    if (config.footer !== undefined) {
        config.footer.content.text = newDescriptionText;
    }
    else if (config.footer === undefined && newDescriptionText !== undefined) {
        config.footer = getDefaultChartText({
            text: newDescriptionText,
            visible: true,
            font: getDefaultFont(TextElements.DescriptionText),
        });
    }
};
/**
 * Gets whether the chart title is visible.
 * @param config - the config object
 */
export const getChartTitleVisibility = (config) => config.title ? (config.title.visible ?? true) : false;
/**
 * Sets whether to the chart title is visible.
 * @param newVisibility - whether to show the chart title or not
 * @param config - the config object
 */
export const setChartTitleVisibility = (newVisibility, config) => {
    if (config.title !== undefined) {
        config.title.visible = newVisibility;
    }
    else if (config.title === undefined) {
        config.title = getDefaultChartText({
            text: "",
            visible: newVisibility,
            font: getDefaultFont(TextElements.ChartTitle),
        });
    }
};
/**
 * Gets whether the description is visible.
 * @param config - the config object
 */
export const getDescriptionVisibility = (config) => config.footer?.visible ?? false;
/**
 * Sets whether the description is visible.
 * @param newVisibility - whether to show the description or not
 * @param config - the config object
 */
export const setDescriptionVisibility = (newVisibility, config) => {
    if (config.footer !== undefined) {
        config.footer.visible = newVisibility;
    }
    else if (config.footer === undefined && newVisibility !== getDescriptionVisibility(config)) {
        config.footer = getDefaultChartText({
            text: "",
            visible: true,
            font: getDefaultFont(TextElements.DescriptionText),
        });
    }
};
