import { TextElements, getDefaultChartText, getDefaultFont, getDefaultLineSymbol } from "@arcgis/charts-shared-utils";
import { cloneDeep } from "lodash-es";
import { setMaxXBound, setMaxYBound, setMinXBound, setMinYBound } from "../../shared/axes";
const setAxisTitleSymbol = (newAxisTitleSymbol, axis) => {
    const prevText = axis.title?.content.text;
    if (axis.title === undefined || newAxisTitleSymbol === undefined) {
        axis.title = getDefaultChartText({
            text: "",
            visible: true,
            font: getDefaultFont(TextElements.AxisTitle),
            type: TextElements.AxisTitle,
        });
    }
    if (newAxisTitleSymbol !== undefined) {
        axis.title.content = newAxisTitleSymbol;
    }
    axis.title.content.text = prevText;
};
/**
 * Gets the x-axis title symbol from the chart config.
 * @param config
 */
export const getXAxisTitleSymbol = (config) => config.axes?.[0]?.title?.content;
/**
 * Sets the x-axis title symbol in the chart config.
 * @param config
 * @param newXAxisTitleSymbol
 */
export const setXAxisTitleSymbol = (newXAxisTitleSymbol, config) => {
    if (config.axes?.[0] !== undefined) {
        setAxisTitleSymbol(newXAxisTitleSymbol, config.axes[0]);
    }
};
/**
 * Gets the y-axis title symbol from the chart config.
 * @param config
 */
export const getYAxisTitleSymbol = (config) => config.axes?.[1]?.title?.content;
/**
 * Sets the y-axis title symbol in the chart config.
 * @param config
 * @param newYAxisTitleSymbol
 */
export const setYAxisTitleSymbol = (newYAxisTitleSymbol, config) => {
    if (config.axes?.[1] !== undefined) {
        setAxisTitleSymbol(newYAxisTitleSymbol, config.axes[1]);
    }
};
/**
 * Gets the axis labels text symbol from the chart config.
 * @param config
 */
export const getAxisLabelsSymbol = (config) => {
    for (const axis of config.axes ?? []) {
        if (axis?.labels !== undefined) {
            return axis.labels.content;
        }
    }
    return undefined;
};
/**
 * Sets the axis labels text symbol in the chart config.
 * @param config
 * @param newAxisLabelsSymbol
 */
export const setAxisLabelsSymbol = (newAxisLabelsSymbol, config) => {
    for (const axis of config.axes ?? []) {
        if (axis !== undefined) {
            const textToRetain = axis.labels?.content.text;
            if (newAxisLabelsSymbol === undefined) {
                axis.labels = getDefaultChartText({
                    text: "",
                    visible: true,
                    font: getDefaultFont(TextElements.AxisLabels),
                    type: TextElements.AxisLabels,
                });
            }
            else {
                axis.labels.content = cloneDeep(newAxisLabelsSymbol);
            }
            axis.labels.content.text = textToRetain;
        }
    }
};
/**
 * Gets the axis lines symbol from the chart config.
 * @param config
 */
export const getAxisLinesSymbol = (config) => {
    for (const axis of config.axes ?? []) {
        if (axis?.lineSymbol !== undefined) {
            return axis.lineSymbol;
        }
    }
    return undefined;
};
/**
 * Sets the axis lines symbol in the chart config.
 * @param config
 * @param newAxisLinesSymbol
 */
export const setAxisLinesSymbol = (newAxisLinesSymbol, config) => {
    for (const axis of config.axes ?? []) {
        if (axis !== undefined) {
            axis.lineSymbol = cloneDeep(newAxisLinesSymbol) ?? getDefaultLineSymbol();
        }
    }
};
/**
 * Gets the grid lines symbol from the chart config.
 * @param config
 */
export const getGridLinesSymbol = (config) => {
    for (const axis of config.axes ?? []) {
        if (axis?.grid !== undefined) {
            return axis.grid;
        }
    }
    return undefined;
};
/**
 * Sets the grid lines symbol in the chart config.
 * @param config
 * @param newGridLinesSymbol
 */
export const setGridLinesSymbol = (newGridLinesSymbol, config) => {
    for (const axis of config.axes ?? []) {
        if (axis !== undefined) {
            axis.grid = cloneDeep(newGridLinesSymbol);
        }
    }
};
/**
 * Get the title text of the axis
 * @param config - the config object
 * @param axisIndex - the index of the axis
 */
export const getAxisTitleText = (axisIndex, config) => config.axes?.[axisIndex]?.title?.content.text ?? "";
/**
 * Set the title text of the axis
 * @param newAxisTitle - the new title text
 * @param axisIndex - the index of the axis
 * @param config - the config object
 */
export const setAxisTitleText = (newAxisTitle, axisIndex, config) => {
    const axis = config.axes?.[axisIndex];
    if (axis?.title !== undefined) {
        axis.title.content.text = newAxisTitle;
    }
    else if (axis !== undefined && axis?.title === undefined) {
        axis.title = getDefaultChartText({
            text: newAxisTitle,
            visible: true,
            font: getDefaultFont(TextElements.AxisTitle),
        });
    }
};
/**
 * Get the visibility of the axis title
 * @param axisIndex - the index of the axis
 * @param config - the config object
 */
export const getAxisTitleVisibility = (axisIndex, config) => config.axes?.[axisIndex]?.title?.visible ?? false;
/**
 * Set the visibility of the axis title
 * @param newVisibility - whether to show the axis title or not
 * @param axisIndex - the index of the axis
 * @param config - the config object
 */
export const setAxisTitleVisibility = (newVisibility, axisIndex, config) => {
    const axis = config.axes?.[axisIndex];
    if (axis?.title !== undefined) {
        axis.title.visible = newVisibility;
    }
    else if (axis !== undefined && axis?.title === undefined) {
        axis.title = getDefaultChartText({
            text: getAxisTitleText(axisIndex, config),
            visible: newVisibility,
            font: getDefaultFont(TextElements.AxisTitle),
        });
    }
};
/**
 * Get whether the X logarithmic scale is enabled
 * @param config - the config object
 */
export const getXLogarithmic = (config) => config.axes?.[0].isLogarithmic ?? false;
/**
 * Set whether the X logarithmic scale is enabled
 * @param config
 * @param enableXLogarithmic
 */
export const setXLogarithmic = async (config, enableXLogarithmic) => {
    if (config.axes?.[0] !== undefined) {
        config.axes[0].isLogarithmic = enableXLogarithmic;
        // Reset the max and min bounds
        await setMaxXBound(config, null);
        await setMinXBound(config, null);
    }
};
/**
 * Get whether the Y logarithmic scale is enabled
 * @param config - the config object
 */
export const getYLogarithmic = (config) => config.axes?.[1]?.isLogarithmic ?? false;
/**
 * Sets whether the Y logarithmic scale is enabled
 * @param config - the config object
 * @param enableYLogarithmic - the value to set
 */
export const setYLogarithmic = async (config, enableYLogarithmic) => {
    if (config.axes?.[1] !== undefined) {
        config.axes[1].isLogarithmic = enableYLogarithmic;
        // Reset the max and min bounds
        await setMaxYBound(config, null);
        await setMinYBound(config, null);
    }
};
