import { WebChartTypes } from "@arcgis/charts-spec";
import { getDefaultBoxPlotElementSymbol, isBoxPlotSeries } from "@arcgis/charts-shared-utils";
import { getShowMeanLines } from "./box-plot-data";
/**
 * get series name of the given series from the in config object using series index
 * @param seriesIndex - index of series in series array
 * @param config - config object
 */
export const getName = (seriesIndex, config) => config?.series?.[seriesIndex]?.name;
/**
 * set series name of the given series in the series array in config object using series index
 * @param newName - new name to be set
 * @param seriesIndex - index of series in series array
 * @param config - config object
 */
export const setName = (newName, seriesIndex, config) => {
    const boxPlotConfig = config;
    if (boxPlotConfig?.series?.[seriesIndex] !== undefined) {
        boxPlotConfig.series[seriesIndex].name = newName;
    }
};
/**
 * get series color of the given series from the series array in config object using series index
 * @param seriesIndex - index of series in series array
 * @param config - config object
 */
export const getColor = (seriesIndex, config) => {
    let seriesColor;
    const series = config.series?.[seriesIndex];
    if (series?.type === WebChartTypes.BoxPlotSeries) {
        seriesColor = series.fillSymbol?.color;
    }
    else if (series?.type === WebChartTypes.LineSeries) {
        seriesColor = series.lineSymbol?.color;
    }
    return seriesColor;
};
/**
 * get color of the box plot series from config object used when show mean is true
 * @param config
 */
export const getMeanLinesBoxColor = (config) => {
    const series = config.series[0];
    return config.showMean === true && isBoxPlotSeries(series) ? series.fillSymbol?.color : undefined;
};
/**
 * set color of the box plot series in config object used when show mean is true
 * @param newColor - new color to be set
 * @param config - config object
 */
export const setMeanLinesBoxColor = (newColor, config) => {
    const series = config.series[0];
    if (config.showMean === true && isBoxPlotSeries(series)) {
        if (series.fillSymbol !== undefined) {
            series.fillSymbol.color = newColor;
        }
        else {
            series.fillSymbol = {
                ...getDefaultBoxPlotElementSymbol(),
                color: newColor,
            };
        }
    }
};
/**
 * set series color of the given series in the series array in config object using series index
 * @param newColor - new color to be set
 * @param seriesIndex - index of series in series array
 * @param config - config object
 */
export const setColor = (newColor, seriesIndex, config) => {
    const boxPlotConfig = config;
    const series = boxPlotConfig?.series?.[seriesIndex];
    if (series?.type === WebChartTypes.BoxPlotSeries && series.fillSymbol !== undefined) {
        series.fillSymbol.color = newColor;
    }
    else if (series?.type === WebChartTypes.LineSeries && series.lineSymbol !== undefined) {
        series.lineSymbol.color = newColor;
        if (series.markerSymbol !== undefined) {
            series.markerSymbol.color = newColor;
        }
    }
};
/**
 * get the order of series from the series array in config object
 * @param config - config object
 */
export const getSeriesOrder = (config) => config.series.map((s) => s.id);
/**
 * set the order of series in the series array in config object using new order of series
 * @param newSeriesOrder - new order of series to be set
 * @param config - config object
 * @returns void
 */
export const setSeriesOrder = (newSeriesOrder, config) => {
    config?.series?.sort((prev, curr) => newSeriesOrder.findIndex((s) => prev.id === s) - newSeriesOrder.findIndex((s) => curr.id === s));
};
/**
 * Move a series in-place from one index to another in the series array in box plot config
 * @param fromIndex - index of the series to be moved
 * @param toIndex - index where the series should be moved
 * @param config - box plot config
 * @returns void
 */
export const moveSeries = (fromIndex, toIndex, config) => {
    const newOrder = getSeriesOrder(config);
    const [seriesToMove] = newOrder.splice(fromIndex, 1);
    newOrder.splice(toIndex, 0, seriesToMove);
    setSeriesOrder(newOrder, config);
};
/**
 * Get the index of the box plot series in the series array in config object when show mean is true.
 *
 * **NOTE**: There is only one box plot series when show mean is true. The function returns '-1' if show mean is false.
 * @param config - config object
 */
export const getBoxPlotSeriesIndexWhenMeanLinesShown = (config) => getShowMeanLines(config)
    ? config.series.findIndex((s) => s.type === WebChartTypes.BoxPlotSeries)
    : -1;
