import { WebChartStackedKinds } from "@arcgis/charts-spec";
import { getDefaultBarChartSeriesSymbol, getDefaultLineChartLineSymbol, getDefaultNumberFormat, getDefaultPercentNumberFormat, isBarChartSeries, isLineChartSeries, } from "@arcgis/charts-shared-utils";
import { getYAxisTitleLabel, setMaxYBound, setMinYBound, updateAxisTitle } from "../../shared/axes";
import { getAggregationType, getNumericFields } from "./serial-chart-data";
/**
 * Get the name of the series for the provided index.
 * @param {number} seriesIndex - The index of the series.
 * @param {ChartConfig} config - The chart configuration.
 * @returns {string | undefined}
 */
export const getSeriesName = (seriesIndex, config) => config.series?.[seriesIndex]?.name;
/**
 * Set the name of the series for the provided index.
 * @param {string} newName - The new name for the series.
 * @param {number} seriesIndex - The index of the series.
 * @param {ChartConfig} config - The chart configuration.
 */
export const setSeriesName = (newName, seriesIndex, config) => {
    if (config.series?.[seriesIndex]) {
        config.series[seriesIndex].name = newName;
    }
};
/**
 * Get the color of the series for the provided index.
 * @param {number} seriesIndex - The index of the series.
 * @param {ChartConfig} config - The chart configuration.
 * @returns {Color | undefined}
 */
export const getSeriesColor = (seriesIndex, config) => {
    const series = config.series?.[seriesIndex];
    let color;
    // use the fillSymbol color for BarChartSeries
    if (isBarChartSeries(series)) {
        color = series.fillSymbol?.color;
    }
    // use the lineSymbol color for LineChartSeries
    else if (isLineChartSeries(series)) {
        color = series.lineSymbol?.color;
    }
    return color;
};
/**
 * Set the color of the series for the provided index.
 * @param {Color | undefined} newColor - The new color for the series.
 * @param {number} seriesIndex - The index of the series.
 * @param {ChartConfig} config - The chart configuration.
 */
export const setSeriesColor = (newColor, seriesIndex, config) => {
    const series = config.series?.[seriesIndex];
    if (series) {
        // set the fillSymbol color for BarChartSeries
        if (isBarChartSeries(series)) {
            series.fillSymbol = {
                ...getDefaultBarChartSeriesSymbol(seriesIndex),
                ...series.fillSymbol,
                color: newColor,
            };
        }
        // set the lineSymbol color for LineChartSeries
        else if (isLineChartSeries(series)) {
            series.lineSymbol = {
                ...getDefaultLineChartLineSymbol(seriesIndex),
                ...series.lineSymbol,
                color: newColor,
            };
        }
    }
};
/**
 * Get the list of series IDs in the order they appear in the chart config.
 * @param {ChartConfig} config - The chart configuration.
 * @returns {WebChartTypes | undefined}
 */
export const getOrderOfSeries = (config) => config.series.map((s) => s.id);
/**
 * Update the order of the series in the chart config.
 * @param {string[]} newSeriesOrder - The new order of the series.
 * @param {ChartConfig} config - The chart configuration.
 */
export const setOrderOfSeries = (newSeriesOrder, config) => {
    config.series?.sort((prev, curr) => newSeriesOrder.findIndex((s) => prev.id === s) - newSeriesOrder.findIndex((s) => curr.id === s));
};
/**
 * Get how the bars/lines should be placed when multiple series are rendered in the same chart, say when "split by" operation is executed on any field or
 * when multiple attributes/fields corresponding to the same set of classes are to be visualized.
 *
 * Currently this property is only used for bar and line series.
 *
 * @param {ChartConfig<SerialChartSeries>} config - The chart configuration.
 * @default sideBySide
 */
export const getStackedType = (config) => config.stackedType ?? WebChartStackedKinds.Side;
/**
 * Set how the bars/lines should be placed when multiple series are rendered in the same chart, say when "split by" operation is executed on any field or
 * when multiple attributes/fields corresponding to the same set of classes are to be visualized.
 *
 * Currently this property is only used for bar and line series.
 *
 * @param {ChartConfig<SerialChartSeries>} props.config - The chart configuration.
 * @param {WebChartStackedKinds | undefined} props.newStackedType - The new stacked type.
 * @param {LayerInfo | undefined} props.layerInfo - The layer information.
 * @default sideBySide
 */
export const setStackedType = async (props) => {
    const { newStackedType, config, layerInfo } = props;
    config.stackedType = newStackedType;
    if (layerInfo !== undefined) {
        // update the y-axis title since the stacked type could affect the y-axis title
        const yAxisTitle = await getYAxisTitleLabel({
            config,
            layerInfo,
            numericFields: getNumericFields(config),
            aggregationType: getAggregationType(config),
        });
        updateAxisTitle(config, 1, yAxisTitle);
    }
    // when multiple bar type changes to Stacked100, use the percent format
    if (config.axes?.[1]) {
        if (newStackedType === WebChartStackedKinds.Stacked100) {
            config.axes[1].valueFormat = getDefaultPercentNumberFormat();
        }
        else {
            // otherwise, use the default number format
            config.axes[1].valueFormat = getDefaultNumberFormat();
        }
    }
    // the y-axis bounds need to be reset given changing the multiple bar type could change the y-axis range
    await setMinYBound(config, null);
    await setMaxYBound(config, null);
};
