import { SupportedLocales, getLocaleInfo } from "@arcgis/charts-shared-utils";
export async function fetchCommonStrings(locale = getLocaleInfo().locale) {
    // rollup does not support dynamic imports https://github.com/rollup/rollup/issues/2463
    // So we use the imports before run time using this approach , the JSON are built into js files in `dist`.
    // https://devtopia.esri.com/WebGIS/arcgis-charts/pull/1830
    let strings = await import("./messages.en.json");
    switch (locale) {
        case SupportedLocales.Arabic:
            strings = await import("./messages.ar.json");
            break;
        case SupportedLocales.Bosnian:
            strings = await import("./messages.bs.json");
            break;
        case SupportedLocales.Bulgarian:
            strings = await import("./messages.bg.json");
            break;
        case SupportedLocales.Catalan:
            strings = await import("./messages.ca.json");
            break;
        case SupportedLocales.Czech:
            strings = await import("./messages.cs.json");
            break;
        case SupportedLocales.Danish:
            strings = await import("./messages.da.json");
            break;
        case SupportedLocales.German:
            strings = await import("./messages.de.json");
            break;
        case SupportedLocales.Greek:
            strings = await import("./messages.el.json");
            break;
        case SupportedLocales.EnglishUSA:
            strings = await import("./messages.en-US.json");
            break;
        case SupportedLocales.Spanish:
            strings = await import("./messages.es.json");
            break;
        case SupportedLocales.Estonian:
            strings = await import("./messages.et.json");
            break;
        case SupportedLocales.French:
            strings = await import("./messages.fr.json");
            break;
        case SupportedLocales.Finnish:
            strings = await import("./messages.fi.json");
            break;
        case SupportedLocales.Hebrew:
            strings = await import("./messages.he.json");
            break;
        case SupportedLocales.Croatian:
            strings = await import("./messages.hr.json");
            break;
        case SupportedLocales.Hungarian:
            strings = await import("./messages.hu.json");
            break;
        case SupportedLocales.Indonesian:
            strings = await import("./messages.id.json");
            break;
        case SupportedLocales.Italian:
            strings = await import("./messages.it.json");
            break;
        case SupportedLocales.Japanese:
            strings = await import("./messages.ja.json");
            break;
        case SupportedLocales.Korean:
            strings = await import("./messages.ko.json");
            break;
        case SupportedLocales.Lithuanian:
            strings = await import("./messages.lt.json");
            break;
        case SupportedLocales.Latvian:
            strings = await import("./messages.lv.json");
            break;
        case SupportedLocales.NorwegianBokml:
            strings = await import("./messages.no.json");
            break;
        case SupportedLocales.Dutch:
            strings = await import("./messages.nl.json");
            break;
        case SupportedLocales.Polish:
            strings = await import("./messages.pl.json");
            break;
        case SupportedLocales.PortugueseBrazil:
            strings = await import("./messages.pt-BR.json");
            break;
        case SupportedLocales.PortuguesePortugal:
            strings = await import("./messages.pt-PT.json");
            break;
        case SupportedLocales.Romanian:
            strings = await import("./messages.ro.json");
            break;
        case SupportedLocales.Russian:
            strings = await import("./messages.ru.json");
            break;
        case SupportedLocales.Serbian:
            strings = await import("./messages.sr.json");
            break;
        case SupportedLocales.Slovak:
            strings = await import("./messages.sk.json");
            break;
        case SupportedLocales.Slovenian:
            strings = await import("./messages.sl.json");
            break;
        case SupportedLocales.Swedish:
            strings = await import("./messages.sv.json");
            break;
        case SupportedLocales.Thai:
            strings = await import("./messages.th.json");
            break;
        case SupportedLocales.Turkish:
            strings = await import("./messages.tr.json");
            break;
        case SupportedLocales.Ukrainian:
            strings = await import("./messages.uk.json");
            break;
        case SupportedLocales.Vietnamese:
            strings = await import("./messages.vi.json");
            break;
        case SupportedLocales.ChineseChina:
            strings = await import("./messages.zh-CN.json");
            break;
        case SupportedLocales.ChineseHongKong:
            strings = await import("./messages.zh-HK.json");
            break;
        case SupportedLocales.ChineseTaiwan:
            strings = await import("./messages.zh-TW.json");
            break;
        case SupportedLocales.English:
        default:
            break;
    }
    return strings;
}
