import { isChartRotated, updateRotation } from "@arcgis/charts-shared-utils";
import { ChartModel } from "../chart-model/chart-model";
export class ModelWithRotation extends ChartModel {
    /**
     * Gets whether the chart is rotated.
     * @returns {boolean}
     */
    getRotatedState() {
        return isChartRotated(this._config);
    }
    /**
     * Sets whether the chart is rotated.
     * @param {boolean} newRotatedState
     */
    setRotatedState(newRotatedState) {
        this._config = updateRotation(newRotatedState, this._config) ?? this._config;
        this.emitConfigUpdate("setRotatedState");
    }
}
