import { getDefaultBoxPlotSeries, getFieldAlias, formatMessage, getDefaultCategoryFormat, getLayerDateFields, defaultBoxPlotCategory, getYList, getDefaultDateFormat, isAbsoluteTruthWhereClause, getDefaultLineSymbol, getDefaultLineChartMarkerSymbol, getDefaultBoxPlotElementSymbol, defaultColorForBoxPlotWithMeanLines, isBoxPlotSeries, isBoxPlotSeriesArray, getSeriesReference, defaultLineSymbolWidth, getDefaultLineChartSeries, } from "@arcgis/charts-shared-utils";
import { RESTFieldType, WebChartOrderDataByTypes, WebChartSortOrderKinds, WebChartTypes } from "@arcgis/charts-spec";
import { cloneDeep, isEmpty, isEqual, memoize } from "lodash-es";
