/**
 * Sets whether the values are standardized.
 */
export const setStandardizeValues = async (newStandardizeValues, config, layerInfo) => {
    config.standardizeValues = newStandardizeValues;
    // update the Y-axis title
    const yAxisTitle = await getYAxisTitleLabel(config, layerInfo);
    updateAxisTitle(config, 1, yAxisTitle);
};
/**
 * Gets the sort order.
 */
export const getSortOrderKind = (config) => {
    let sortOrder = BoxPlotDataSortingKinds.xAxisAsc;
    // extract sorting field and the order
    const orderType = config.orderOptions?.data?.orderType ?? WebChartOrderDataByTypes.Category;
    const orderBy = config.orderOptions?.data?.orderBy ?? WebChartSortOrderKinds.Ascending;
    switch (orderType) {
        case WebChartOrderDataByTypes.Category:
            sortOrder = Array.isArray(orderBy)
                ? BoxPlotDataSortingKinds.customSort
                : orderBy === WebChartSortOrderKinds.Ascending
                    ? BoxPlotDataSortingKinds.xAxisAsc
                    : BoxPlotDataSortingKinds.xAxisDesc;
            break;
        case WebChartOrderDataByTypes.Mean:
            sortOrder =
                orderBy === WebChartSortOrderKinds.Ascending
                    ? BoxPlotDataSortingKinds.meanAsc
                    : BoxPlotDataSortingKinds.meanDesc;
            break;
        case WebChartOrderDataByTypes.Median:
            sortOrder =
                orderBy === WebChartSortOrderKinds.Ascending
                    ? BoxPlotDataSortingKinds.medianAsc
                    : BoxPlotDataSortingKinds.medianDesc;
            break;
        default:
            break;
    }
    return sortOrder;
};
/**
 * Sets the sort order.
 */
export const setSortOrderKind = (newSortOrderKind, config, orderByList) => {
    let orderType = WebChartOrderDataByTypes.Category;
    let orderBy = WebChartSortOrderKinds.Ascending;
    config.orderOptions = config.orderOptions ?? {};
    if (newSortOrderKind === BoxPlotDataSortingKinds.customSort && orderByList !== undefined) {
        config.orderOptions.data = {
            orderType: WebChartOrderDataByTypes.Category,
            orderBy: orderByList,
        };
    }
    else {
        switch (newSortOrderKind) {
            case BoxPlotDataSortingKinds.xAxisAsc: {
                orderType = WebChartOrderDataByTypes.Category;
                orderBy = WebChartSortOrderKinds.Ascending;
                break;
            }
            case BoxPlotDataSortingKinds.xAxisDesc: {
                orderType = WebChartOrderDataByTypes.Category;
                orderBy = WebChartSortOrderKinds.Descending;
                break;
            }
            case BoxPlotDataSortingKinds.meanAsc: {
                orderType = WebChartOrderDataByTypes.Mean;
                orderBy = WebChartSortOrderKinds.Ascending;
                break;
            }
            case BoxPlotDataSortingKinds.meanDesc: {
                orderType = WebChartOrderDataByTypes.Mean;
                orderBy = WebChartSortOrderKinds.Descending;
                break;
            }
            case BoxPlotDataSortingKinds.medianAsc: {
                orderType = WebChartOrderDataByTypes.Median;
                orderBy = WebChartSortOrderKinds.Ascending;
                break;
            }
            case BoxPlotDataSortingKinds.medianDesc: {
                orderType = WebChartOrderDataByTypes.Median;
                orderBy = WebChartSortOrderKinds.Descending;
                break;
            }
            default:
                break;
        }
        config.orderOptions.data = {
            orderType,
            orderBy,
        };
    }
};
/**
 * Gets whether the mean lines are shown.
 * @param config
 */
export const getShowMeanLines = (config) => config.showMean ?? false;
/**
 * Sets whether the mean lines are shown. Rebuilds the series array.
 * @param newShowMeanLines Whether to show the mean lines
 * @param config The config to update
 * @param previousMeanLinesBoxColor The previous color of the boxes when mean lines were enabled
 */
export const setShowMeanLines = async (newShowMeanLines = false, config, previousMeanLinesBoxColor) => {
    const newPreviousMeanLinesBoxColor = !newShowMeanLines ? getMeanLinesBoxColor(config) : previousMeanLinesBoxColor;
    // determine whether the value has changed, treating `undefined` and `false` as the same
    const isChanged = (newShowMeanLines ?? false) !== (config.showMean ?? false);
    config.showMean = newShowMeanLines;
    if (isChanged) {
        let newSeries;
        if (newShowMeanLines) {
            // convert all box plot series to line series
            // TODO: if we want the color of the boxes for mean lines to keep their color upon switching `showMean` we can save the whole
            // original series and pass it to `convertSeriesForMeanLinesEnabled()` through its `originalBoxSeriesForMeanLines` input
            newSeries = isBoxPlotSeriesArray(config.series)
                ? await convertSeriesForMeanLinesEnabled(config.series)
                : config.series;
        }
        else {
            // convert all line series back to box plot series
            newSeries = convertSeriesForMeanLinesDisabled(config.series);
        }
        if (newSeries) {
            // making sure we keep the sort between the series when switching the way they are rendered
            newSeries = reorderSeries({
                formerSeries: config.series,
                newSeries,
                showMean: newShowMeanLines,
            });
            config.series = newSeries;
        }
    }
    if (newShowMeanLines && previousMeanLinesBoxColor !== undefined) {
        setMeanLinesBoxColor(previousMeanLinesBoxColor, config);
    }
    return { previousMeanLinesBoxColor: newPreviousMeanLinesBoxColor };
};
/**
 * Get the order by list from the config. The order by list is only used when the sort order kind is custom sort.
 * @param  {ChartConfig<WebChartBoxPlotSeries} config - The chart config.
 *
 * @returns string[] - The order by list.
 */
export const getOrderByList = (config) => {
    let orderByList = [];
    const orderType = config.orderOptions?.data?.orderType;
    const sortOrderKind = getSortOrderKind(config);
    if (sortOrderKind === BoxPlotDataSortingKinds.customSort && orderType === WebChartOrderDataByTypes.Category) {
        orderByList = config.orderOptions?.data?.orderBy;
    }
    return orderByList;
};
/**
 * Set the order by list in the config. The order by list is only used when the sort order kind is custom sort.
 * @param  {string[]} values - The order by list.
 * @param  {ChartConfig<WebChartBoxPlotSeries} config - The chart config.
 */
export const setOrderByList = (values, config) => {
    config.orderOptions ??= {};
    const orderType = config.orderOptions?.data?.orderType;
    const sortOrderKind = getSortOrderKind(config);
    if (sortOrderKind === BoxPlotDataSortingKinds.customSort && orderType === WebChartOrderDataByTypes.Category) {
        config.orderOptions.data = {
            orderType: WebChartOrderDataByTypes.Category,
            orderBy: values,
        };
    }
};
/**
 * Get the data filter from the config.
 * @param  {ChartConfig<WebChartBarChartSeries} config - The chart config.
 * @returns WebChartDataFilters | undefined - The data filter.
 */
export const getDataFilter = (config) => config.dataFilters;
/**
 * Set the data filter in the config.
 * @param  {WebChartDataFilters | undefined} dataFilter - The data filter.
 * @param  {ChartConfig<WebChartBarChartSeries} config - The chart config.
 */
export const setDataFilter = (dataFilter, config) => {
    if (dataFilter !== undefined) {
        config.dataFilters = dataFilter;
    }
    else {
        delete config.dataFilters;
    }
};
/**
 * Get the custom sort values from the config.
 * @param  {ChartConfig<WebChartBarChartSeries} config - The chart config.
 * @param  {LayerInfo} layerInfo - The layer info.
 *
 * @returns string[] - The custom sort values.
 */
export const getCustomSortValues = async (config, layerInfo) => {
    const category = getCategory(config);
    const valueType = getXAxisValueFormat(config)?.type;
    const numericFields = getNumericFields(config);
    let customSortValues = [];
    if (valueType === WebChartTypes.CategoryAxisFormat) {
        if (category === defaultBoxPlotCategory) {
            customSortValues = numericFields;
        }
        else if (category.trim() !== "" && category !== defaultBoxPlotCategory) {
            customSortValues = await getCategoryLabels({
                layerInfo,
                config,
                category,
            });
        }
    }
    return customSortValues;
};
