import { cloneDeep, isEmpty, isNull, memoize } from "lodash-es";
import { RESTStatisticType, WebChartOrderDataByTypes, WebChartSortOrderKinds, defaultPieFromFieldsName, defaultPieFromFieldsValue, } from "@arcgis/charts-spec";
import { getDefaultPieChartSeries, getFieldAlias, formatMessage, getDefaultAggregationTypeLabel, getDefaultLegend, } from "@arcgis/charts-shared-utils";
import { fetchCommonStrings } from "../../t9n/common";
import { PieChartDataSortingKinds, PieChartDataModes } from "../../interfaces/common";
import { getYAxisTitleLabel, updateAxisTitle } from "../../shared/axes";
/**
 * Gets the category field.
 */
export const getCategory = (config) => config.series?.[0]?.x ?? "";
/**
 * Returns default sort order of "[Value field] DESC".
 * @category Private
 */
function getDefaultSortOrder(config, mode) {
    const pieSeries = config.series?.[0];
    const valueField = mode === PieChartDataModes.Category ? pieSeries.y : defaultPieFromFieldsValue;
    return [`${valueField} ${WebChartSortOrderKinds.Descending}`];
}
/**
 * Gets the sort order kind.
 */
export const getSortOrderKind = (config, mode) => {
    // label clockwise sort order by default
    let sortOrderKind = PieChartDataSortingKinds.valueDesc;
    const orderType = config.orderOptions?.data?.orderType ?? getDefaultSortOrder(config, mode);
    const order = config.orderOptions?.data?.orderBy ?? WebChartSortOrderKinds.Descending;
    if (orderType === WebChartOrderDataByTypes.Category && Array.isArray(order)) {
        sortOrderKind = PieChartDataSortingKinds.customSort;
    }
    else if (orderType === WebChartOrderDataByTypes.Category) {
        if (order === WebChartSortOrderKinds.Ascending) {
            sortOrderKind = PieChartDataSortingKinds.labelAsc;
        }
        else {
            sortOrderKind = PieChartDataSortingKinds.labelDesc;
        }
    }
    else if (order === WebChartSortOrderKinds.Ascending) {
        sortOrderKind = PieChartDataSortingKinds.valueAsc;
    }
    else {
        sortOrderKind = PieChartDataSortingKinds.valueDesc;
    }
    return sortOrderKind;
};
/**
 * Gets the numeric fields.
 */
export const getNumericFields = memoize(
// without memoizing, the worst case efficiency is O(series.length), best case is O(1)
// with memoizing, the worst case efficiency for repeated calls becomes O(1)
(config) => {
    let numericFields = [];
    // map numeric fields from series if there is no split-by field
    numericFields = config.series?.[0]?.query?.outStatistics?.map((statisticField) => statisticField.onStatisticField) ?? [config.series?.[0]?.y ?? ""];
    return [...new Set(numericFields.filter((field) => field !== ""))];
}, 
// using series array address as key, since a change to the numeric fields will always rebuild a fresh series array
(config) => config.series);
/**
 * Gets the aggregation type.
 */
export const getAggregationType = (config, mode) => {
    const numericFields = getNumericFields(config);
    let aggregationType;
    // if in "Fields" mode and no numeric fields are selected yet, the aggregation type should be Sum as a placeholder
    if (mode === PieChartDataModes.Fields && numericFields.length === 0) {
        aggregationType = RESTStatisticType.Sum;
    }
    else {
        aggregationType = config.series?.[0]?.query?.outStatistics?.[0]?.statisticType ?? RESTStatisticType.NoAggregation;
    }
    return aggregationType;
};
/**
 * Gets a default series value.
 */
async function getDefaultSeriesValue(props) {
    const { config, layerInfo, mode } = props;
    const { objectIdField } = layerInfo ?? {};
    const defaultSeries = await getDefaultPieChartSeries(objectIdField);
    const category = getCategory(config);
    const dataLabelsVisible = config.series[0]?.dataLabels?.visible ?? false;
    const defaultValue = {
        ...defaultSeries,
        ...{
            x: mode === PieChartDataModes.Category ? category : defaultPieFromFieldsName,
            slices: config.series?.[0]?.slices,
            dataLabels: { ...defaultSeries.dataLabels, visible: dataLabelsVisible },
            innerRadiusSize: config.series?.[0]?.innerRadius,
            sliceGrouping: config.series?.[0]?.sliceGrouping,
            dataLabelsCharacterLimit: config.series?.[0]?.dataLabelsCharacterLimit,
            numericValueFormat: config.series?.[0]?.numericValueFormat,
            percentValueFormat: config.series?.[0]?.percentValueFormat,
            displayNumericValueOnDataLabel: config.series?.[0]?.displayNumericValueOnDataLabel,
            displayPercentageOnDataLabel: config.series?.[0]?.displayPercentageOnDataLabel,
        },
    };
    const statisticType = getAggregationType(config, mode);
    if (statisticType !== RESTStatisticType.NoAggregation && objectIdField !== undefined) {
        const outStatisticFieldName = `${statisticType}_${objectIdField}_0`.toUpperCase();
        defaultValue.y = mode === PieChartDataModes.Category ? outStatisticFieldName : defaultPieFromFieldsValue;
        defaultValue.query = {
            outStatistics: mode === PieChartDataModes.Category
                ? [
                    {
                        statisticType,
                        onStatisticField: objectIdField,
                        outStatisticFieldName,
                    },
                ]
                : [],
        };
    }
    if (mode === PieChartDataModes.Category && defaultValue.query !== undefined) {
        defaultValue.query.groupByFieldsForStatistics = [category];
    }
    else {
        delete defaultValue.query?.groupByFieldsForStatistics;
    }
    return cloneDeep(defaultValue);
}
/**
 * Gets the proper chart title given the current config.
 */
async function getChartTitle(config, layerInfo, mode) {
    const { layerFieldsInfo, objectIdField } = layerInfo;
    const category = getCategory(config);
    const numericFields = getNumericFields(config);
    const aggregationType = getAggregationType(config, mode);
    const hasObjectIDField = numericFields.length === 0 || numericFields.includes(objectIdField);
    const aggregationTypeLabel = await getDefaultAggregationTypeLabel(aggregationType);
    const numericFieldAliasesList = hasObjectIDField
        ? ""
        : numericFields.map((currentField) => getFieldAlias(layerFieldsInfo ?? [], currentField)).join(", ");
    const categoryAlias = getFieldAlias(layerFieldsInfo ?? [], category);
    let title = "";
    /* Building the title according to the values 4 parameters. See details in issue https://devtopia.esri.com/WebGIS/arcgis-charts/issues/2080.
        - aggregation type
        - aggregation field(s)
        - category
        - split-by-field
      */
    const commonStrings = await fetchCommonStrings();
    // Similar to `isPartialConfig()` function but determined from UI options instead of from chart config
    let isPartialChart;
    if (mode === PieChartDataModes.Fields) {
        // In "Fields" mode, Pie Chart with no numeric fields is partial
        isPartialChart = numericFields.length === 0;
    }
    else {
        // In "Category" mode, Pie Chart missing category field is partial
        isPartialChart = category === "";
    }
    if (isPartialChart) {
        title = commonStrings.chartType.pieChart;
    }
    else if (aggregationType === RESTStatisticType.NoAggregation) {
        title = formatMessage(commonStrings.titleLabels.aggrFieldsByCategory, {
            aggregationFields: hasObjectIDField ? objectIdField : numericFieldAliasesList,
            category: categoryAlias,
        });
    }
    else if (aggregationType === RESTStatisticType.Count) {
        title = formatMessage(commonStrings.titleLabels.aggrTypeCategory, {
            aggregationType: aggregationTypeLabel,
            category: categoryAlias,
        });
    }
    else if (numericFields.length > 0) {
        if (categoryAlias === defaultPieFromFieldsName || isEmpty(categoryAlias) || isNull(categoryAlias)) {
            title = formatMessage(commonStrings.titleLabels.aggrTypeOfAggrFields, {
                aggregationType: aggregationTypeLabel,
                aggregationFields: numericFieldAliasesList,
            });
        }
        else {
            title = formatMessage(commonStrings.titleLabels.aggrTypeOfAggrFieldsByCategory, {
                aggregationType: aggregationTypeLabel,
                aggregationFields: numericFieldAliasesList,
                category: categoryAlias,
            });
        }
    }
    return title;
}
/**
 * Sets the sort order by updating the config accordingly.
 */
export const setSortOrderKind = (props) => {
    const { newSortOrderKind, config, orderByList } = props;
    config.orderOptions ??= {};
    if (newSortOrderKind === PieChartDataSortingKinds.customSort && orderByList !== undefined) {
        config.orderOptions.data = {
            orderType: WebChartOrderDataByTypes.Category,
            orderBy: [...orderByList],
        };
    }
    else {
        config.orderOptions.data = {
            orderType: newSortOrderKind === PieChartDataSortingKinds.labelAsc ||
                newSortOrderKind === PieChartDataSortingKinds.labelDesc
                ? WebChartOrderDataByTypes.Category
                : WebChartOrderDataByTypes.YValue,
            orderBy: newSortOrderKind === PieChartDataSortingKinds.labelAsc || newSortOrderKind === PieChartDataSortingKinds.valueAsc
                ? WebChartSortOrderKinds.Ascending
                : WebChartSortOrderKinds.Descending,
        };
    }
};
/**
 * Sets the category field by updating the config accordingly.
 */
export const setCategory = async (props) => {
    const { newCategory, config, layerInfo, mode } = props;
    const seriesLength = config.series?.length ?? 0;
    const currentSortOrderKind = getSortOrderKind(config, mode);
    // update each series
    config.series.forEach((series) => {
        // update the X-field value
        series.x = mode === PieChartDataModes.Category ? newCategory : defaultPieFromFieldsName;
        // update the query
        if (series.query !== undefined && newCategory !== defaultPieFromFieldsName) {
            series.query.groupByFieldsForStatistics = [newCategory];
        }
        else {
            delete series.query?.groupByFieldsForStatistics;
        }
    });
    if (seriesLength === 0) {
        // create series from the default since there are no series to update
        const series = [await getDefaultSeriesValue({ config, layerInfo, mode })];
        config.series = series;
    }
    // update the sort order
    if (currentSortOrderKind !== undefined) {
        const orderByList = getOrderByList(config, mode);
        setSortOrderKind({
            newSortOrderKind: currentSortOrderKind,
            config,
            orderByList,
        });
    }
    if (layerInfo !== undefined) {
        // update the Y-axis title
        const yAxisTitle = await getYAxisTitleLabel({
            config,
            layerInfo,
            numericFields: getNumericFields(config),
            aggregationType: getAggregationType(config, mode),
        });
        updateAxisTitle(config, 1, yAxisTitle);
        // update the chart title
        if (config.title) {
            config.title.content.text = await getChartTitle(config, layerInfo, mode);
        }
        // if we are removing the category field (mode === Fields), we want to filter out the objectID field from the numeric fields
        const numericFields = mode === PieChartDataModes.Fields
            ? getNumericFields(config).filter((field) => field !== layerInfo.objectIdField)
            : getNumericFields(config);
        await setNumericFields({ newNumericFields: numericFields, config, layerInfo, mode });
    }
};
/**
 * Builds a new series array based on a list of numeric fields and aggregation type.
 * @param props.numericFields
 * @param props.aggregationType
 * @param props.config
 * @param props.layerInfo
 */
async function buildNewSeries(props) {
    const { numericFields, aggregationType, config, layerInfo, mode } = props;
    const sortOrder = getSortOrderKind(config, mode);
    const seriesValues = [];
    const defaultSeriesValue = await getDefaultSeriesValue({
        config,
        layerInfo,
        mode,
    });
    const newSeries = {
        ...defaultSeriesValue,
        ...{
            query: {
                ...defaultSeriesValue.query,
            },
        },
    };
    if (numericFields.length <= 1) {
        newSeries.query.where = defaultSeriesValue.query?.where;
    }
    else {
        delete newSeries.query.where;
    }
    if (aggregationType !== RESTStatisticType.NoAggregation) {
        if (mode === PieChartDataModes.Category) {
            if (numericFields.length > 0) {
                const numericalField = numericFields[0];
                const yFieldName = `${aggregationType}_${numericFields[0]}_0`.toUpperCase();
                newSeries.y = yFieldName;
                newSeries.query.outStatistics = [
                    {
                        ...(defaultSeriesValue.query?.outStatistics?.[0] ?? {}),
                        ...{
                            onStatisticField: numericalField,
                            outStatisticFieldName: yFieldName,
                            statisticType: aggregationType,
                        },
                    },
                ];
            }
        }
        else {
            newSeries.y = defaultPieFromFieldsValue;
            for (let i = 0; i < numericFields.length; i += 1) {
                newSeries.query.outStatistics?.push({
                    onStatisticField: numericFields[i],
                    outStatisticFieldName: numericFields[i],
                    statisticType: aggregationType,
                });
            }
        }
    }
    else {
        // remove the query if the aggregation type is NoAggregation and update the Y-field
        newSeries.y = numericFields[0];
        newSeries.query = {};
    }
    // update the sort order
    const orderByList = getOrderByList(config, mode);
    setSortOrderKind({
        newSortOrderKind: sortOrder,
        config,
        orderByList,
    });
    seriesValues.push(newSeries);
    config.series = seriesValues;
}
/**
 * Sets the aggregation type by updating the config accordingly.
 */
export const setAggregationType = async (props) => {
    const { newAggregationType, config, layerInfo, mode } = props;
    const numericFields = getNumericFields(config);
    // build new series
    await buildNewSeries({
        config,
        layerInfo,
        numericFields,
        aggregationType: newAggregationType,
        mode,
    });
    // update the Y-axis title
    const yAxisTitle = await getYAxisTitleLabel({
        config,
        layerInfo,
        numericFields: getNumericFields(config),
        aggregationType: getAggregationType(config, mode),
    });
    updateAxisTitle(config, 1, yAxisTitle);
    // update the chart title
    if (config.title) {
        config.title.content.text = await getChartTitle(config, layerInfo, mode);
    }
    // update the legend display type
    const displayNumericValue = config.series?.[0]?.displayNumericValueOnDataLabel;
    const displayPercentage = config.series?.[0]?.displayPercentageOnDataLabel;
    config.legend = {
        ...(config.legend ?? getDefaultLegend()),
        displayNumericValue,
        displayPercentage,
    };
};
/**
 * Sets the numeric fields by updating the config accordingly.
 */
export const setNumericFields = async (props) => {
    let { newNumericFields } = props;
    const { config, layerInfo, mode } = props;
    const { objectIdField } = layerInfo ?? {};
    let aggregationType = getAggregationType(config, mode);
    // if using an empty string numeric field change it to an empty array
    newNumericFields = newNumericFields === "" ? [] : newNumericFields;
    // if using a single string numeric field place it into an array
    newNumericFields = typeof newNumericFields === "string" ? [newNumericFields] : newNumericFields;
    if (objectIdField !== undefined) {
        if (newNumericFields.length === 0 && mode === PieChartDataModes.Category) {
            newNumericFields.push(objectIdField);
            // when no numeric field is selected, the aggregationType should be Count
            aggregationType = RESTStatisticType.Count;
        }
        else if (aggregationType === RESTStatisticType.Count &&
            newNumericFields.length >= 1 &&
            !newNumericFields.includes(objectIdField)) {
            // when changing from objectIDField to actual numericField, the aggregationType should be Sum not Count
            aggregationType = RESTStatisticType.Sum;
        }
    }
    if (layerInfo !== undefined) {
        // build new series
        await buildNewSeries({
            config,
            layerInfo,
            numericFields: newNumericFields,
            aggregationType,
            mode,
        });
        // update the Y-axis title
        const yAxisTitle = await getYAxisTitleLabel({
            config,
            layerInfo,
            numericFields: getNumericFields(config),
            aggregationType: getAggregationType(config, mode),
        });
        updateAxisTitle(config, 1, yAxisTitle);
        // update the chart title
        if (config.title) {
            config.title.content.text = await getChartTitle(config, layerInfo, mode);
        }
    }
    else {
        config.series.forEach((series) => {
            delete series.query;
            series.y = newNumericFields[0];
        });
    }
};
/**
 * Get the order by list from the config. The order by list is only used when the sort order kind is custom sort.
 * @param  {ChartConfig<WebChartBarChartSeries>} config - The chart config.
 * @param  {PieChartDataModes} mode - The chart mode.
 *
 * @returns string[] - The order by list.
 */
export const getOrderByList = (config, mode) => {
    let orderByList = [];
    const orderType = config.orderOptions?.data?.orderType;
    const sortOrderKind = getSortOrderKind(config, mode);
    const orderBy = config.orderOptions?.data?.orderBy;
    if (sortOrderKind === PieChartDataSortingKinds.customSort &&
        orderType === WebChartOrderDataByTypes.Category &&
        Array.isArray(orderBy)) {
        orderByList = orderBy;
    }
    return orderByList;
};
/**
 * Set the order by list in the config. The order by list is only used when the sort order kind is custom sort.
 * @param  {string[]} props.values - The order by list.
 * @param  {ChartConfig<WebChartBarChartSeries>} props.config - The chart config.
 * @param  {PieChartDataModes} props.mode - The chart mode.
 */
export const setOrderByList = (props) => {
    const { values, config, mode } = props;
    config.orderOptions ??= {};
    const orderType = config.orderOptions?.data?.orderType;
    const sortOrderKind = getSortOrderKind(config, mode);
    if (sortOrderKind === PieChartDataSortingKinds.customSort && orderType === WebChartOrderDataByTypes.Category) {
        config.orderOptions.data = {
            orderType: WebChartOrderDataByTypes.Category,
            orderBy: [...values],
        };
    }
};
