import { WebChartLegendPositions } from "@arcgis/charts-spec";
import { TextElements, getDefaultChartText, getDefaultFont, getDefaultLegend } from "@arcgis/charts-shared-utils";
/**
 * Gets the legend visibility from the chart config.
 * @param config
 */
export const getLegendVisibility = (config) => {
    let visibility = false;
    if (config.legend !== undefined) {
        visibility = config.legend.visible ?? true;
    }
    return visibility;
};
/**
 * Sets the legend visibility in the chart config.
 * @param newLegendVisibility
 * @param config
 */
export const setLegendVisibility = (newLegendVisibility, config) => {
    if (config.legend === undefined) {
        config.legend = getDefaultLegend();
    }
    config.legend.visible = newLegendVisibility;
};
/**
 * Gets the legend title symbol from the chart config.
 * @param config
 */
export const getLegendTitleSymbol = (config) => config.legend?.title?.content;
/**
 * Sets the legend title symbol in the chart config.
 * @param config
 * @param newLegendTitleSymbol
 */
export const setLegendTitleSymbol = (newLegendTitleSymbol, config) => {
    if (config.legend === undefined) {
        config.legend = getDefaultLegend();
    }
    if (config.legend.title === undefined || newLegendTitleSymbol === undefined) {
        config.legend.title = getDefaultChartText({
            text: "",
            visible: true,
            font: getDefaultFont(TextElements.LegendTitle),
            type: TextElements.LegendTitle,
        });
    }
    if (newLegendTitleSymbol !== undefined) {
        config.legend.title.content = newLegendTitleSymbol;
    }
};
/**
 * Gets the legend text symbol from the chart config.
 * @param config
 */
export const getLegendTextSymbol = (config) => config.legend?.body;
/**
 * Sets the legend text symbol in the chart config.
 * @param config
 * @param newLegendTextSymbol
 */
export const setLegendTextSymbol = (newLegendTextSymbol, config) => {
    if (config.legend === undefined) {
        config.legend = getDefaultLegend();
    }
    config.legend.body =
        newLegendTextSymbol ??
            getDefaultChartText({
                text: "",
                visible: true,
                font: getDefaultFont(TextElements.LegendText),
                type: TextElements.LegendText,
            }).content;
};
/**
 * Get the legend title text
 * @param config - the config object
 */
export const getLegendTitleText = (config) => config.legend?.title?.content.text ?? "";
/**
 * Set the legend title text
 * @param newLegendTitle - the new legend title
 * @param config - the config object
 */
export const setLegendTitleText = (newLegendTitle, config) => {
    if (config.legend?.title !== undefined) {
        config.legend.title.content.text = newLegendTitle;
    }
    else if (config.legend === undefined && newLegendTitle !== undefined) {
        const newLegend = getDefaultLegend();
        if (newLegend.title !== undefined) {
            newLegend.title.content.text = newLegendTitle;
            newLegend.title.visible = true;
            config.legend = newLegend;
        }
    }
};
/**
 * Determine whether to show the legend title or not
 * @param config - the config object
 */
export const getLegendTitleVisibility = (config) => config.legend?.title?.visible ?? false;
/**
 * Set the visibility of the legend title
 * @param newVisibility - whether to show the legend title or not
 * @param config - the config object
 */
export const setLegendTitleVisibility = (newVisibility, config) => {
    if (config.legend?.title !== undefined) {
        config.legend.title.visible = newVisibility;
    }
    else if (config.legend !== undefined && config.legend?.title === undefined && newVisibility) {
        config.legend.title = getDefaultChartText({
            text: getLegendTitleText(config),
            visible: newVisibility,
            font: getDefaultFont(TextElements.LegendTitle),
            type: TextElements.LegendTitle,
        });
    }
    else if (config.legend === undefined) {
        const newLegend = getDefaultLegend();
        newLegend.title = getDefaultChartText({
            text: getLegendTitleText(config),
            visible: newVisibility,
            font: getDefaultFont(TextElements.LegendTitle),
            type: TextElements.LegendTitle,
        });
        config.legend = newLegend;
    }
};
/**
 * Get the legend position
 * @param config - the config object
 */
export const getLegendPosition = (config) => config.legend?.position ?? WebChartLegendPositions.Right;
/**
 * Set the legend position
 * @param newLegendPosition - the new legend position
 * @param config - the config object
 */
export const setLegendPosition = (newLegendPosition, config) => {
    if (config.legend !== undefined) {
        config.legend.position = newLegendPosition;
    }
    else if (config.legend === undefined) {
        // if there's no legend we create a default one and assign the position
        const newLegend = getDefaultLegend();
        newLegend.position = newLegendPosition;
        config.legend = newLegend;
    }
};
