import { WebChartTypes } from "@arcgis/charts-spec";
import { setMaxYBound, setMinYBound } from "../../shared/axes";
/**
 * Gets the time interval size.
 * @param config The chart configuration.
 */
export const getTimeIntervalSize = (config) => "timeIntervalSize" in config.series[0] ? config.series[0].timeIntervalSize : undefined;
/**
 * Gets the time interval units.
 * @param config The chart configuration.
 */
export const getTimeIntervalUnits = (config) => "timeIntervalUnits" in config.series[0] ? config.series[0].timeIntervalUnits : undefined;
/**
 * Gets the time aggregation type.
 * @param config The chart configuration.
 */
export const getTimeAggregationType = (config) => "timeAggregationType" in config.series[0] ? config.series[0].timeAggregationType : undefined;
/**
 * Gets whether to trim incomplete time interval.
 * @param config The chart configuration.
 */
export const getTrimIncompleteTimeInterval = (config) => "trimIncompleteTimeInterval" in config.series[0] ? config.series[0].trimIncompleteTimeInterval : undefined;
/**
 * Gets the null policy.
 * @param config The chart configuration.
 */
export const getNullPolicy = (config) => "nullPolicy" in config.series[0] ? config.series[0].nullPolicy : undefined;
/**
 * Sets the time interval size by updating the config accordingly.
 * @param newTimeIntervalSize The new value for the interval size.
 * @param config The chart configuration.
 */
export const setTimeIntervalSize = (newTimeIntervalSize, config) => {
    config?.series?.forEach((series) => {
        if (series.type === WebChartTypes.BarSeries || series.type === WebChartTypes.LineSeries) {
            series.timeIntervalSize = newTimeIntervalSize;
        }
    });
    // reset the Y-axis minimum and maximum since the time interval size changing could change the y-axis range
    setMinYBound(config, null);
    setMaxYBound(config, null);
};
/**
 * Sets the null policy by updating the config accordingly.
 * @param newNullPolicy The new value for the null policy.
 * @param config The chart configuration.
 */
export const setNullPolicy = (newNullPolicy, config) => {
    config?.series?.forEach((series) => {
        if (series.type === WebChartTypes.BarSeries || series.type === WebChartTypes.LineSeries) {
            series.nullPolicy = newNullPolicy;
        }
    });
    // reset the Y-axis minimum and maximum since the null policy changing could change the y-axis range
    setMinYBound(config, null);
    setMaxYBound(config, null);
};
