/**
 * Sets whether to trim incomplete time interval by updating the config accordingly.
 * @param newTrimIncompleteTimeInterval The new value for the trim incomplete time interval property.
 * @param config The chart configuration.
 */
export const setTrimIncompleteTimeInterval = (newTrimIncompleteTimeInterval, config) => {
    config.series?.forEach((series) => {
        if (series.type === WebChartTypes.BarSeries || series.type === WebChartTypes.LineSeries) {
            series.trimIncompleteTimeInterval = newTrimIncompleteTimeInterval;
        }
    });
    // reset the Y-axis minimum and maximum since the trim incomplete time interval changing could change the y-axis range
    setMinYBound(config, null);
    setMaxYBound(config, null);
};
/**
 * Resets the time interval by setting its size to 0.
 * @param config The chart configuration.
 */
export const resetTimeInterval = (config) => {
    setTimeIntervalSize(0, config);
};
