/**
 * Adds a guide to the chart config.
 * @param props.config The chart config.
 * @param props.name The name of the guide to add.
 * @param props.index The index of the guide to add.
 * @param props.orientation The axis orientation of the guide.
 */
export const addGuide = (props) => {
    const { config, name, index, orientation } = props;
    const axis = orientation === GuideOrientation.X ? config.axes?.[0] : config.axes?.[1];
    if (axis !== undefined) {
        axis.guides = axis.guides ?? [];
        const guide = {
            type: WebChartTypes.Guide,
            name,
            start: axis.valueFormat.type === WebChartTypes.DateAxisFormat ? "" : 0,
            style: getDefaultLineSymbol(),
        };
        if (index !== undefined && index < axis.guides.length) {
            axis.guides.splice(index, 0, guide);
        }
        else {
            axis.guides.push(guide);
        }
    }
};
/**
 * Deletes a guide from the chart config.
 * @param props.config The chart config.
 * @param props.index The index of the guide to delete.
 * @param props.orientation The axis orientation of the guide.
 */
export const deleteGuide = (props) => {
    const { config, index, orientation } = props;
    const axis = orientation === GuideOrientation.X ? config.axes?.[0] : config.axes?.[1];
    if (axis?.guides !== undefined) {
        if (index !== undefined && index < axis.guides.length) {
            axis.guides.splice(index, 1);
        }
        else {
            axis.guides.pop();
        }
    }
};
