/**
 * Gets the numeric fields.
 * @param {ChartConfig<SerialChartSeries>} config
 */
export const getNumericFields = memoize(
// without memoizing, the worst case efficiency is O(series.length), best case is O(1)
// with memoizing, the worst case efficiency for repeated calls becomes O(1)
(config) => {
    let numericFields = [];
    if (getSplitByField(config) === "") {
        // map numeric fields from series outStatistics if there is no split-by field
        numericFields =
            config?.series
                .map((series) => {
                const onStatisticFields = series.query?.outStatistics?.map((outStatistic) => outStatistic.onStatisticField) ?? [];
                return onStatisticFields?.length > 0 ? onStatisticFields : series.y;
            })
                .flat() ?? [];
    }
    else {
        // get the single numeric field from the first series if there is a split-by field
        const series = config?.series?.[0];
        const numericField = series?.query?.outStatistics?.[0]?.onStatisticField ?? [series?.y].flat()[0];
        numericFields = numericField !== undefined ? [numericField] : [];
    }
    return [...new Set(numericFields.filter((field) => field !== ""))];
}, 
// using series array address as key, since a change to the numeric fields will always rebuild a fresh series array
(config) => config?.series);
/**
 * Sets the category field by updating the config accordingly.
 * @param {string[]} newNumericFields - The new numeric fields.
 * @param {ChartConfig<SerialChartSeries>} config - The chart config.
 * @param {LayerInfo} layerInfo - The layer info.
 */
export const setNumericFields = async (newNumericFields, config, layerInfo) => {
    const { objectIdField } = layerInfo;
    let aggregationType = getAggregationType(config);
    if (newNumericFields.length === 0) {
        newNumericFields.push(objectIdField);
        // When no numeric field is selected, the aggregationType should be Count
        aggregationType = RESTStatisticType.Count;
    }
    else if (aggregationType === RESTStatisticType.Count &&
        newNumericFields.length >= 1 &&
        !newNumericFields.includes(objectIdField)) {
        // When changing from objectIDField to actual numericField, the aggregationType should be Sum not Count
        aggregationType = RESTStatisticType.Sum;
    }
    // Get existing split-by field
    const splitByField = getSplitByField(config);
    // Build new series
    await buildNewSeries({
        splitByField,
        numericFields: newNumericFields,
        aggregationType,
        config,
        layerInfo,
    });
    // update the Y-axis title
    const yAxisTitle = await getYAxisTitleLabel({
        config,
        layerInfo,
        numericFields: getNumericFields(config),
        aggregationType: getAggregationType(config),
    });
    updateAxisTitle(config, 1, yAxisTitle);
    // update the chart title
    if (config.title) {
        config.title.content.text = await getChartTitle(config, layerInfo);
    }
    // update the Y-axis minimum and maximum
    // Note: The Y-axis bounds need to be reset since changing the numeric fields could change the Y-axis range
    await setMinYBound(config, null);
    await setMaxYBound(config, null);
    // update the 'integer only' option
    setAxesIntegerOnly(config, layerInfo);
};
/**
 * Set the x-axis field by updating the config accordingly.
 * @param {string} newXAxisField - The new x-axis field.
 * @param {ChartConfig} config - The chart config.
 * @param {LayerInfo} layerInfo - The layer info.
 */
export const setXAxisField = async (newXAxisField, config, layerInfo) => {
    const { layerFieldsInfo } = layerInfo ?? {};
    const seriesLength = config?.series?.length ?? 0;
    const previousXAxisField = getXAxisField(config);
    let xAxisValueFormat;
    // Check if the chart type
    const hasBarChartSeries = config.series.some((series) => isBarChartSeries(series));
    const isLineChart = isLineChartSeriesArray(config.series);
    if (layerFieldsInfo !== undefined) {
        const dateFieldNames = getLayerDateFields(layerFieldsInfo);
        if (!dateFieldNames.includes(previousXAxisField) && dateFieldNames.includes(newXAxisField)) {
            // from non-date field to date field
            xAxisValueFormat = getDefaultDateFormat();
        }
        else if (dateFieldNames.includes(previousXAxisField) && !dateFieldNames.includes(newXAxisField)) {
            // from date field to non-date field
            if (hasBarChartSeries) {
                xAxisValueFormat = getDefaultCategoryFormat();
            }
            else {
                xAxisValueFormat = getDefaultNumberFormat();
            }
        }
        // update the valueFormat to category format if the new x-axis field is a string field
        const newCategoryField = layerFieldsInfo.find((field) => field.name === newXAxisField);
        if (newCategoryField?.type === RESTFieldType.String) {
            xAxisValueFormat = getDefaultCategoryFormat();
        }
        if (xAxisValueFormat !== undefined) {
            // update the valueFormat for the X-axis since the new category may be of a different field type
            await setXAxisValueFormat(config, xAxisValueFormat);
        }
    }
    if (seriesLength === 0) {
        // create series from the default since there are no series to update
        config.series = [await getDefaultSeriesValue({ config, layerInfo })];
    }
    // update the X-axis field and query for each series
    config.series.forEach((series) => {
        // update the X-field value
        series.x = newXAxisField;
        // update the query
        if (series.query !== undefined) {
            series.query.groupByFieldsForStatistics = [newXAxisField];
        }
    });
    // update the X-axis title
    const xAxisTitle = getFieldAlias(layerFieldsInfo, newXAxisField);
    updateAxisTitle(config, 0, xAxisTitle);
    // update the Y-axis title
    if (layerInfo !== undefined) {
        const yAxisTitle = await getYAxisTitleLabel({
            config,
            layerInfo,
            numericFields: getNumericFields(config),
            aggregationType: getAggregationType(config),
        });
        updateAxisTitle(config, 1, yAxisTitle);
        // update the chart title
        if (config.title) {
            config.title.content.text = await getChartTitle(config, layerInfo);
        }
        // updating the 'integer only' option
        setAxesIntegerOnly(config, layerInfo);
        const numericFields = getNumericFields(config);
        await setNumericFields(numericFields, config, layerInfo);
    }
    // update the Y-axis minimum and maximum
    // note: the y-axis bounds need to be reset since changing the category could change the y-axis range
    await setMinYBound(config, null);
    await setMaxYBound(config, null);
    // additional updates for x-axis field changes in line charts
    if (isLineChart) {
        // Update the X-axis minimum and maximum
        await setMinXBound(config, null);
        await setMaxXBound(config, null);
        // Update the series time interval size
        setTimeIntervalSize(0, config);
        // Setting the null policy according to the axis type
        const nullPolicy = xAxisValueFormat?.type === WebChartTypes.DateAxisFormat
            ? WebChartNullPolicyTypes.Null
            : WebChartNullPolicyTypes.Interpolate;
        setNullPolicy(nullPolicy, config);
    }
};
/**
 * Sets the x-axis 'integer only' option to true when the x-axis field is an integer. NOTE: only available for line charts.
 * Sets the y-axis 'integer only' option to true when:
 * - the aggregation type is Count
 * - the aggregation type is No aggregation, and all the chosen numeric fields are typed as integer fields
 *
 * @param {ChartConfig<SerialChartSeries>} config - The chart config
 * @param {LayerInfo} layerInfo - The layer info
 */
function setAxesIntegerOnly(config, layerInfo) {
    let integerOnlyValues = false;
    const aggregationType = getAggregationType(config);
    const numericFields = getNumericFields(config);
    if (aggregationType === RESTStatisticType.Count) {
        integerOnlyValues = true;
    }
    else if (aggregationType === RESTStatisticType.NoAggregation) {
        // looping through the numeric fields and checking if all of them are from the integer type
        integerOnlyValues = numericFields.every((fieldName) => {
            const iField = layerInfo.layerFieldsInfo.find((field) => field.name === fieldName);
            return iField !== undefined ? isIntegerField(iField) : false;
        });
    }
    setIntegerOnlyValuesInAxis(config, 1, integerOnlyValues);
    const hasLineChartSeries = config.series.some((series) => isLineChartSeries(series));
    if (hasLineChartSeries) {
        // Finding the x-axis field info based on its name
        const xAxisField = getXAxisField(config);
        const xAxisFieldObject = layerInfo.layerFieldsInfo.find((field) => field.name === xAxisField);
        // If the field is an Integer, we set the axis `integerOnlyValues` to true to avoid float values to be displayed
        const xAxisIntegerOnlyValues = xAxisFieldObject !== undefined && isIntegerField(xAxisFieldObject);
        setIntegerOnlyValuesInAxis(config, 0, xAxisIntegerOnlyValues);
    }
}
/**
 * Gets the sort order kind.
 */
export const getSortOrderKind = (config) => {
    // x-axis ascending sort order by default
    let sortOrderKind = SerialChartDataSortingKinds.xAxisAsc;
    // extract sorting field and the order
    const orderType = config.orderOptions?.data?.orderType ?? WebChartOrderDataByTypes.Category;
    const order = config.orderOptions?.data?.orderBy ?? WebChartSortOrderKinds.Ascending;
    if (orderType === WebChartOrderDataByTypes.Category && Array.isArray(order)) {
        sortOrderKind = SerialChartDataSortingKinds.customSort;
    }
    else if (orderType === WebChartOrderDataByTypes.Category) {
        if (order === WebChartSortOrderKinds.Ascending) {
            sortOrderKind = SerialChartDataSortingKinds.xAxisAsc;
        }
        else {
            sortOrderKind = SerialChartDataSortingKinds.xAxisDesc;
        }
    }
    else if (order === WebChartSortOrderKinds.Ascending) {
        sortOrderKind = SerialChartDataSortingKinds.yAxisAsc;
    }
    else {
        sortOrderKind = SerialChartDataSortingKinds.yAxisDesc;
    }
    return sortOrderKind;
};
/**
 * Build a series array based on a list of numeric fields and aggregation type,
 * when the chart has no category field.
 * @param {string[]} props.numericFields - The numeric fields to include in the series.
 * @param {RESTStatisticType} props.aggregationType - The type of aggregation to use.
 * @param {ChartConfig<SerialChartSeries>} props.config - The chart configuration.
 * @param {LayerInfo} props.layerInfo - Information about the data layer.
 */
export async function buildSeriesFromFields(props) {
    const { numericFields, aggregationType, config, layerInfo } = props;
    let customColorWasApplied = false;
    const defaultSeriesValue = await getDefaultSeriesValue({
        config,
        layerInfo,
        seriesIndex: 0,
    });
    const commonStrings = await fetchCommonStrings();
    /**
     * NOTE: since the chart has no category field, we default `x` and `y` to some defaulted values instead of empty strings
     * so that they are different from each other to ensure the information is stored properly for later use.
     * We use this distinction to determine whether the later chart config was created using fields first or not.
     */
    const newSeries = {
        ...defaultSeriesValue,
        id: `series_${Date.now()}`,
        name: commonStrings.axisLabels.values,
        x: defaultBarLineFromFieldsName,
        y: defaultBarLineFromFieldsValue,
    };
    const outStatistics = [];
    // adding an outStatistic per numeric field
    for (const y of numericFields) {
        outStatistics.push({
            onStatisticField: y,
            outStatisticFieldName: `${y}_0`.toUpperCase(),
            statisticType: aggregationType,
        });
    }
    newSeries.query = { outStatistics };
    const originalSeries = config.series[0];
    // restore styling info from the original series
    if (originalSeries !== undefined) {
        // restore previous id
        newSeries.id = originalSeries.id;
        if (!config.colorMatch) {
            if (isBarChartSeries(originalSeries) && isBarChartSeries(newSeries)) {
                // for bar charts, restore fillSymbol
                newSeries.fillSymbol = originalSeries.fillSymbol;
            }
            else if (isLineChartSeries(newSeries) && isLineChartSeries(originalSeries)) {
                // for line charts, restore lineSymbol and markerSymbol
                newSeries.lineSymbol = originalSeries.lineSymbol;
                newSeries.markerSymbol = originalSeries.markerSymbol;
            }
            customColorWasApplied = true;
        }
        // restore previous name and id
        newSeries.id = originalSeries.id;
        // restore previous name only if it is different from the default name
        if (originalSeries.name !== defaultSeriesValue.name) {
            newSeries.name = originalSeries.name;
        }
    }
    // if there is only one series, we can always retain the previous custom color
    if (config.series.length === 1 && !config.colorMatch) {
        if (isBarChartSeries(newSeries)) {
            newSeries.fillSymbol = config.series[0].fillSymbol;
        }
        else if (isLineChartSeries(newSeries)) {
            newSeries.lineSymbol = config.series[0].lineSymbol;
            newSeries.markerSymbol = config.series[0].markerSymbol;
        }
        customColorWasApplied = true;
    }
    // update color type to single-color if a custom color was applied
    config.colorMatch = !customColorWasApplied;
    return [newSeries];
}
/**
 * Build a series array based on a list of numeric fields and aggregation type.
 * @param {string[]} props.numericFields - The numeric fields to include in the series.
 * @param {"count" | "sum" | "min" | "max" | "avg" | "stddev" | "var" | "percentile_cont" | "percentile_disc" | "envelope-aggregate" | "centroid-aggregate" | "convex-hull-aggregate" | "no_aggregation"} props.aggregationType - The type of aggregation to use.
 * @param {ChartConfig<SerialChartSeries>} props.config - The chart configuration.
 * @param {LayerInfo} props.layerInfo - Information about the data layer.
 */
export async function buildSeriesUsingNumericFields(props) {
    const { numericFields, aggregationType, config, layerInfo } = props;
    const { layerFieldsInfo, objectIdField } = layerInfo;
    const seriesValues = [];
    let customColorWasApplied = false;
    const fieldNames = numericFields.map((field, index) => `${aggregationType}_${field}_${index}`.toUpperCase());
    for (let i = 0; i < numericFields.length; i += 1) {
        // eslint-disable-next-line no-await-in-loop
        const defaultSeriesValue = await getDefaultSeriesValue({
            config,
            layerInfo,
            seriesIndex: i,
        });
        const yFieldName = fieldNames[i].toUpperCase();
        const numericalField = numericFields[i];
        let newSeries = {
            ...defaultSeriesValue,
            ...{
                query: {
                    ...defaultSeriesValue.query,
                },
            },
        };
        if (numericalField !== objectIdField) {
            newSeries = {
                ...newSeries,
                id: numericalField,
                name: getFieldAlias(layerFieldsInfo, numericalField),
            };
        }
        // the value of `y` and `outStatistics` are different whether the aggregation type is blank
        let y = numericalField;
        if (newSeries.query !== undefined) {
            if (aggregationType !== RESTStatisticType.NoAggregation) {
                y = yFieldName;
                newSeries.query.outStatistics = [
                    {
                        ...(defaultSeriesValue.query?.outStatistics?.[0] ?? {}),
                        ...{
                            onStatisticField: numericalField,
                            outStatisticFieldName: yFieldName,
                            statisticType: aggregationType,
                        },
                    },
                ];
            }
            else {
                delete newSeries.query.outStatistics;
            }
        }
        newSeries.y = y;
        // find original series that matches the current numerical field and wasn't using split-by
        const originalSeries = config.series.find((s) => s.query?.outStatistics?.[0]?.onStatisticField === numericalField && s.query.where === undefined);
        // restore styling info from the original series
        // NOTE: we don't restore the series if the previous config was created using fields first
        if (originalSeries !== undefined && typeof originalSeries.y === "string" && originalSeries.y.trim() !== "") {
            if (!config.colorMatch) {
                if (isBarChartSeries(newSeries) && isBarChartSeries(originalSeries)) {
                    // for bar charts, restore fillSymbol
                    newSeries.fillSymbol = originalSeries.fillSymbol;
                }
                else if (isLineChartSeries(newSeries) && isLineChartSeries(originalSeries)) {
                    // for line charts, restore lineSymbol and markerSymbol
                    newSeries.lineSymbol = originalSeries.lineSymbol;
                    newSeries.markerSymbol = originalSeries.markerSymbol;
                }
                customColorWasApplied = true;
            }
            const defaultSeries = await getDefaultSeriesValue({
                config,
                layerInfo,
                seriesIndex: i,
            });
            // restore previous id
            newSeries.id = originalSeries.id;
            // restore previous id and name only if they are different from the default id and name
            if (originalSeries.id !== defaultSeries.id && originalSeries.name !== defaultSeries.name) {
                newSeries.id = originalSeries.id;
                newSeries.name = originalSeries.name;
            }
        }
        seriesValues.push(newSeries);
    }
    // if there is only one series, we can always retain the previous custom color
    if (config.series.length === 1 && seriesValues.length === 1 && !config.colorMatch) {
        if (isBarChartSeries(config.series[0]) && isBarChartSeries(seriesValues[0])) {
            seriesValues[0].fillSymbol = config.series[0].fillSymbol;
        }
        else if (isLineChartSeries(config.series[0]) && isLineChartSeries(seriesValues[0])) {
            seriesValues[0].lineSymbol = config.series[0].lineSymbol;
            seriesValues[0].markerSymbol = config.series[0].markerSymbol;
        }
        customColorWasApplied = true;
    }
    // update color type to single-color if a custom color was applied
    config.colorMatch = !customColorWasApplied;
    return seriesValues;
}
/**
 * Build a series array based on a split-by field and aggregation type.
 * @param {string} props.splitByField - The split-by field.
 * @param {string[]} props.numericFields - The numeric fields to include in the series.
 * @param {"count" | "sum" | "min" | "max" | "avg" | "stddev" | "var" | "percentile_cont" | "percentile_disc" | "envelope-aggregate" | "centroid-aggregate" | "convex-hull-aggregate" | "no_aggregation"} props.aggregationType - The type of aggregation to use.
 * @param {ChartConfig<SerialChartSeries>} props.config - The chart configuration.
 */
export async function buildSeriesUsingSplitByField(props) {
    const { splitByField, numericFields, aggregationType, config, layerInfo } = props;
    const { layer, layerFieldsInfo } = layerInfo;
    const { fetchNullValues } = config?.series?.[0]?.query ?? {};
    // get the X-axis field
    const xAxisField = getXAxisField(config);
    const xAxisFieldForSplitBy = xAxisField !== defaultBarLineFromFieldsName ? xAxisField : undefined;
    // fetch split-by field information
    const { categoriesArray, fieldName, splitByFieldType, numericalField } = await getSplitByFieldInfos({
        config,
        layer,
        layerFieldsInfo,
        xAxisField: xAxisFieldForSplitBy,
        aggregationFields: numericFields,
        aggregationType,
        splitByField,
        fetchNullValues,
    });
    let seriesValues = [];
    let customColorWasApplied = false;
    // if all the categories have been filtered out, we build the series using the numeric fields to act as a dummy series so that the data empty message shows up properly instead of an invalid config error.
    if (categoriesArray.length === 0) {
        seriesValues = await buildSeriesUsingNumericFields({
            numericFields,
            aggregationType,
            config,
            layerInfo,
        });
        // we assign empty string as split by value when the categoriesArray is empty
        for (const series of seriesValues) {
            if (series.query) {
                series.query.where = `${splitByField}=''`;
            }
        }
    }
    else {
        for (let i = 0; i < categoriesArray.length; i += 1) {
            const uniqueValue = categoriesArray[i];
            const yFieldName = `${fieldName}_${i}`.toUpperCase();
            // eslint-disable-next-line no-await-in-loop
            const defaultSeriesValue = await getDefaultSeriesValue({
                config,
                layerInfo,
                seriesIndex: i,
            });
            // stringified uniqueValue for id and name
            const idAndName = `${uniqueValue}`;
            const newSeriesValue = {
                ...defaultSeriesValue,
                ...{
                    id: idAndName,
                    name: idAndName,
                    y: yFieldName,
                    query: {
                        ...defaultSeriesValue.query,
                        ...{
                            where: `${splitByField}=${splitByFieldType === RESTFieldType.String && uniqueValue !== null ? `'${uniqueValue}'` : uniqueValue}`,
                            outStatistics: aggregationType !== RESTStatisticType.NoAggregation
                                ? [
                                    {
                                        ...(defaultSeriesValue.query?.outStatistics?.[0] ?? {}),
                                        ...{
                                            onStatisticField: numericalField,
                                            outStatisticFieldName: yFieldName,
                                            statisticType: aggregationType,
                                        },
                                    },
                                ]
                                : undefined,
                        },
                    },
                },
            };
            if (aggregationType === RESTStatisticType.NoAggregation) {
                if (newSeriesValue.query !== undefined) {
                    delete newSeriesValue.query.outStatistics;
                    delete newSeriesValue.query.groupByFieldsForStatistics;
                }
                newSeriesValue.y = numericalField;
            }
            const originalSeries = config.series.find((s) => String(getSeriesSplitByValue(s)).trim().replace(/'/gu, "") === idAndName);
            // restore styling info from the original series
            if (originalSeries !== undefined) {
                if (!config.colorMatch) {
                    if (isBarChartSeries(newSeriesValue) && isBarChartSeries(originalSeries)) {
                        // for bar charts, restore fillSymbol
                        newSeriesValue.fillSymbol = originalSeries.fillSymbol;
                    }
                    else if (isLineChartSeries(newSeriesValue) && isLineChartSeries(originalSeries)) {
                        // for line charts, restore lineSymbol and markerSymbol
                        newSeriesValue.lineSymbol = originalSeries.lineSymbol;
                        newSeriesValue.markerSymbol = originalSeries.markerSymbol;
                    }
                    customColorWasApplied = true;
                }
                // restore previous id and name
                newSeriesValue.id = originalSeries.id;
                newSeriesValue.name = originalSeries.name;
            }
            seriesValues.push(newSeriesValue);
        }
    }
    // update color type to single-color if a custom color was applied
    config.colorMatch = !customColorWasApplied;
    return seriesValues;
}
/**
 * Builds a new series array based on the provided properties.
 * @param props.splitByField - The field to split the data by.
 * @param props.numericFields - The numeric fields to include in the series.
 * @param props.aggregationType - The type of aggregation to use.
 * @param props.config - The chart configuration.
 * @param props.layerInfo - Information about the data layer.
 */
export const buildNewSeries = async (props) => {
    const { splitByField, numericFields, aggregationType, config, layerInfo } = props;
    const { x } = config.series[0];
    const sortOrder = getSortOrderKind(config);
    const chartSubType = getChartSubType(config.series);
    if (splitByField === "" || numericFields.length > 1) {
        // If the category (x) is not defined, we build the series as for a chart from fields
        if (x === defaultBarLineFromFieldsName ||
            x === undefined ||
            x.trim() === "" ||
            chartSubType === ChartSubType.BarAndLineFromFields) {
            config.series = await buildSeriesFromFields({
                config,
                layerInfo,
                numericFields,
                aggregationType,
            });
        }
        else {
            config.series = await buildSeriesUsingNumericFields({
                config,
                layerInfo,
                numericFields,
                aggregationType,
            });
        }
    }
    else {
        config.series = await buildSeriesUsingSplitByField({
            config,
            layerInfo,
            splitByField,
            numericFields,
            aggregationType,
        });
    }
    const orderByList = getOrderByList(config);
    setSortOrderKind(sortOrder, config, orderByList);
};
/**
 * Get the default series name.
 * @param {ChartConfig} config - The chart config.
 * @param {number} seriesIndex - The series index.
 * @param {LayerInfo} layerInfo - The layer info.
 */
export const getDefaultSeriesName = async (config, seriesIndex, layerInfo) => {
    const series = config.series?.[seriesIndex];
    const splitByField = getSplitByField(config);
    let defaultName;
    const commonStrings = await fetchCommonStrings();
    const chartSubType = getChartSubType(config.series);
    if (chartSubType === ChartSubType.BarAndLineFromFields) {
        defaultName = commonStrings.axisLabels.values;
    }
    // if we are not using split-by, use the numeric field to name the series
    else if (isEmpty(splitByField)) {
        const numericField = series.query?.outStatistics?.[0]?.onStatisticField;
        // don't use numeric field as name if it is the objectID field
        if (numericField !== undefined && numericField !== layerInfo.objectIdField) {
            defaultName = getFieldAlias(layerInfo.layerFieldsInfo, numericField);
        }
    }
    else {
        defaultName = String(getSeriesSplitByValue(series)).trim().replace(/'/gu, "");
    }
    return defaultName ?? series.id;
};
/**
 * Set the order by list in the config. The order by list is only used when the sort order kind is custom sort.
 * @param  {string[]} values - The order by list.
 * @param  {ChartConfig<WebChartBarChartSeries} config - The chart config.
 */
export const setOrderByList = (values, config) => {
    config.orderOptions ??= {};
    const orderType = config.orderOptions?.data?.orderType;
    const sortOrderKind = getSortOrderKind(config);
    if (sortOrderKind === SerialChartDataSortingKinds.customSort && orderType === WebChartOrderDataByTypes.Category) {
        config.orderOptions.data = {
            orderType: WebChartOrderDataByTypes.Category,
            orderBy: [...values],
        };
    }
};
/**
 * Get the data filter from the config.
 * @param  {ChartConfig} config - The chart config.
 * @returns WebChartDataFilters | undefined - The data filter.
 */
export const getDataFilter = (config) => config.dataFilters;
/**
 * Set the data filter in the config.
 * @param  {WebChartDataFilters | undefined} dataFilter - The data filter.
 * @param  {ChartConfig} config - The chart config.
 */
export const setDataFilter = (dataFilter, config) => {
    if (dataFilter !== undefined) {
        config.dataFilters = dataFilter;
    }
    else {
        delete config.dataFilters;
    }
};
/**
 * Get the custom sort values from the config.
 * @param  {ChartConfig<WebChartBarChartSeries} config - The chart config.
 * @param  {LayerInfo} layerInfo - The layer info.
 *
 * @returns string[] - The custom sort values.
 */
export const getCustomSortValues = async (config, layerInfo) => {
    const category = getXAxisField(config);
    const valueType = getXAxisValueFormat(config)?.type;
    const numericFields = getNumericFields(config);
    const subType = getChartSubType(config.series);
    let customSortValues = [];
    if (valueType === WebChartTypes.CategoryAxisFormat) {
        if (subType === ChartSubType.BarAndLineFromFields || category === defaultBarLineFromFieldsName) {
            customSortValues = numericFields.map((field) => getFieldAlias(layerInfo?.layerFieldsInfo, field));
        }
        else if (category.trim() !== "" && category !== defaultBarLineFromFieldsName && layerInfo !== undefined) {
            customSortValues = await getCategoryLabels({
                layerInfo,
                config,
                category,
            });
        }
    }
    return customSortValues;
};
