/**
 * Gets the series id from the config
 */
export const getSeriesId = (seriesIndex, config) => config.series[seriesIndex]?.id;
/**
 * Gets the series index from the config
 */
export const getSeriesIndex = (seriesId, config) => config.series.findIndex((s) => s.id === seriesId);
/**
 * Gets the length of the series from the config
 */
export const getSeriesLength = (config) => config.series.length ?? 0;
