/* eslint no-underscore-dangle: ["error", { "allow": ["_chartType"] }] */
import { updateSeriesVisibility, convertChartSpec, getFeatureLayer, fetchLayerFieldsInfo, getChartSubType, } from "@arcgis/charts-shared-utils";
import { getBackgroundColor, getDescriptionSymbol, getTitleSymbol, setBackgroundColor, setDescriptionSymbol, setTitleSymbol, } from "./utils/format";
import { getChartTitleVisibility, getDescriptionText, getDescriptionVisibility, getTitleText, setChartTitleVisibility, setDescriptionText, setDescriptionVisibility, setTitleText, } from "./utils/general";
import { getSeriesId, getSeriesIndex, getSeriesLength } from "./utils/series";
import { getFetchNULLValues, setFetchNULLValues } from "./utils/data";
export class ChartModel extends EventTarget {
    constructor() {
        // ---------------------------------------------------------------------------------------------
        // Protected Properties
        // ---------------------------------------------------------------------------------------------
        super(...arguments);
        /**
         * Promise that resolves when the setup process is complete.
         *
         * **Note: This promise does not need to be awaited if the `setup` method has already been awaited. It is simply provided as a way to await the setup process from other functions if needed to avoid race conditions.**
         */
        this.setupFinished = new Promise((resolve) => {
            this.resolveSetup = resolve;
        });
    }
    /**
     * Checks if the current chart model is for the same chart as another chart model.
     * @param {ChartModel | undefined} otherModel - The other chart model to compare to.
     * @returns {boolean}
     */
    isSameChartAs(otherModel) {
        const otherLayer = otherModel?.getLayer();
        return (otherLayer !== undefined &&
            this._config.id === otherModel?.getChartId() &&
            this.layerInfo?.layer.id === otherLayer.id);
    }
    /**
     * Gets the current chart configuration object.
     * @returns {ChartConfig}
     */
    getConfig() {
        return this._config;
    }
    /**
     * Sets the chart configuration object.
     * @param {ChartConfig} newConfig - The new configuration object.
     */
    setConfig(newConfig) {
        this._config = newConfig;
        this.emitConfigUpdate("setConfig");
    }
    /**
     * Gets the chart type.
     * @returns {ChartTypes}
     */
    getChartType() {
        return this._chartType;
    }
    /**
     * Gets the associated layer.
     * @returns {SupportedLayer | undefined}
     */
    getLayer() {
        return this.layerInfo?.layer;
    }
    /**
     * Gets the chart id.
     * @returns {string | undefined}
     */
    getChartId() {
        return this._config.id;
    }
    /**
     * Sets the chart id.
     * @param {string} newId
     */
    setChartId(newId) {
        this._config.id = newId;
    }
    // ---------------------------------------------------------------------------------------------
    // General Methods
    // ---------------------------------------------------------------------------------------------
    /**
     * Gets the chart title text.
     * @returns {string}
     */
    getTitleText() {
        return getTitleText(this._config);
    }
    /**
     * Sets the chart title text.
     * @param {string} newTitle - The new title text.
     */
    setTitleText(newTitle) {
        setTitleText(newTitle, this._config);
        this.emitConfigUpdate("setTitleText");
    }
    /**
     * Gets the chart description text.
     * @returns {string}
     */
    getDescriptionText() {
        return getDescriptionText(this._config);
    }
    /**
     * Sets the chart description text.
     * @param {string} newDescription - The new description text.
     */
    setDescriptionText(newDescription) {
        setDescriptionText(newDescription, this._config);
        this.emitConfigUpdate("setDescriptionText");
    }
    /**
     * Gets whether the chart title is visible or not.
     *
     * `true`: Chart title is visible.
     *
     * `false`: Chart title is hidden.
     *
     * @returns {boolean}
     *
     * **Note: Even if the title is visible, it may not be displayed if the title text is empty.**
     */
    getChartTitleVisibility() {
        return getChartTitleVisibility(this._config);
    }
    /**
     * Sets whether the chart title is visible or not.
     *
     * `true`: Show chart title.
     *
     * `false`: Hide chart title.
     *
     * @param {boolean} newVisibility - The new visibility state of the chart title.
     *
     * **Note: Even if the title is visible, it may not be displayed if the title text is empty.**
     */
    setChartTitleVisibility(newVisibility) {
        setChartTitleVisibility(newVisibility, this._config);
        this.emitConfigUpdate("setChartTitleVisibility");
    }
    /**
     * Gets whether the chart description is visible or not.
     *
     * `true`: Description is visible.
     *
     * `false`: Description is hidden.
     *
     * @returns {boolean}
     *
     * **Note: Even if the description is visible, it may not be displayed if the description text is empty.**
     */
    getDescriptionVisibility() {
        return getDescriptionVisibility(this._config);
    }
    /**
     * Sets whether the chart description is visible or not.
     *
     * `true`: Show description.
     *
     * `false`: Hide description.
     *
     * @param {boolean} newVisibility - The new visibility state of the description.
     *
     * **Note: Even if the description is visible, it may not be displayed if the description text is empty.**
     */
    setDescriptionVisibility(newVisibility) {
        setDescriptionVisibility(newVisibility, this._config);
        this.emitConfigUpdate("setDescriptionVisibility");
    }
    // ---------------------------------------------------------------------------------------------
    // Format Methods
    // ---------------------------------------------------------------------------------------------
    /**
     * Gets the title text symbol. Contains styling information such as font, color, and symbol style.
     * @returns {WebChartTextSymbol | undefined}
     */
    getTitleSymbol() {
        return getTitleSymbol(this._config);
    }
    /**
     * Sets the title text symbol. Contains styling information such as font, color, and symbol style.
     * @param {WebChartTextSymbol | undefined} newTitleSymbol - The new title symbol.
     */
    setTitleSymbol(newTitleSymbol) {
        setTitleSymbol(newTitleSymbol, this._config);
        this.emitConfigUpdate("setTitleSymbol");
    }
    /**
     * Gets the description text symbol. Contains styling information such as font, color, and symbol style.
     * @returns {WebChartTextSymbol | undefined}
     */
    getDescriptionSymbol() {
        return getDescriptionSymbol(this._config);
    }
    /**
     * Sets the description text symbol. Contains styling information such as font, color, and symbol style.
     * @param {WebChartTextSymbol | undefined} newDescriptionSymbol - The new description symbol.
     */
    setDescriptionSymbol(newDescriptionSymbol) {
        setDescriptionSymbol(newDescriptionSymbol, this._config);
        this.emitConfigUpdate("setDescriptionSymbol");
    }
    /**
     * Gets the background color of the chart.
     * @returns {Color | undefined}
     */
    getBackgroundColor() {
        return getBackgroundColor(this._config);
    }
    /**
     * Sets the background color of the chart.
     * @param {Color | undefined} newBackgroundColor - The new background color.
     */
    setBackgroundColor(newBackgroundColor) {
        setBackgroundColor(newBackgroundColor, this._config);
        this.emitConfigUpdate("setBackgroundColor");
    }
    // ---------------------------------------------------------------------------------------------
    // Series Methods
    // ---------------------------------------------------------------------------------------------
    /**
     * Sets the visibility of a series.
     * @param {boolean} props.visible
     * @param {string} props.seriesId
     * @param {OverlayTerms} props.overlayType
     * @param {string} props.sliceId
     * @param {WebChartDataItem} props.sliceData
     */
    setSeriesVisibility(props) {
        updateSeriesVisibility({ detail: props }, this._config);
        this.emitConfigUpdate("setSeriesVisibility");
    }
    /**
     * Gets the series ID for a given series index.
     *
     * If the series index is out of bounds, this function will return `undefined`.
     *
     * @param {number} seriesIndex
     * @returns {string | undefined}
     */
    getSeriesId(seriesIndex) {
        return getSeriesId(seriesIndex, this._config);
    }
    /**
     * Gets the series index for a given series ID.
     *
     * If the series ID is not found, this function will return `undefined`.
     *
     * @param {string} seriesId
     * @returns {number | undefined}
     */
    getSeriesIndex(seriesId) {
        return getSeriesIndex(seriesId, this._config);
    }
    /**
     * Gets the length of the series array.
     * @returns {number}
     */
    getSeriesLength() {
        return getSeriesLength(this._config);
    }
    /**
     * Get the chart sub type of the series.
     * @returns {ChartSubType}
     */
    getChartSubType() {
        return getChartSubType(this._config.series);
    }
    // ---------------------------------------------------------------------------------------------
    // Data Methods
    // ---------------------------------------------------------------------------------------------
    /**
     * Set whether to fetch `null` values for them to be counted or displayed as categories on the chart.
     * @param  {ChartConfig<WebChartBarChartSeries} config - The chart config.
     *
     * @returns boolean - The fetch NULL values.
     */
    setFetchNULLValues(fetchNULLValues) {
        setFetchNULLValues(fetchNULLValues, this._config);
    }
    /**
     * Get whether to fetch `null` values for them to be counted or displayed as categories on the chart.
     *
     * @returns boolean - The fetch NULL values.
     */
    getFetchNULLValues() {
        return getFetchNULLValues(this._config);
    }
    // ---------------------------------------------------------------------------------------------
    // Protected Methods
    // ---------------------------------------------------------------------------------------------
    /**
     * Emits a config update event.
     * @category Protected
     */
    emitConfigUpdate(functionCalled) {
        this.dispatchEvent(new CustomEvent("arcgisChartsModelConfigChange", {
            detail: { config: this._config, functionCalled },
        }));
    }
    /**
     * A function that loads the config based on the provided props.
     * @category Protected
     * @param params.layer The layer to be used as the data source for the chart.
     * @param params.iLayer The layer description to use if the layer is not provided
     * @param params.config The config object to be loaded.
     */
    async loadConfigFromLayer(params) {
        const { layer, iLayer, config } = params;
        let theLayer = layer;
        if (!theLayer && iLayer) {
            // if the layer is not provided we create an instance from the iLayer
            theLayer = await getFeatureLayer(iLayer);
        }
        // Pulling and storing the info regarding the layer
        if (theLayer) {
            const layerFieldsInfo = await fetchLayerFieldsInfo(theLayer, true);
            const { objectIdField } = theLayer;
            this.layerInfo = {
                layer: theLayer,
                layerFieldsInfo,
                objectIdField,
            };
        }
        // Generating a new config if not provided, or converting the existing one
        this._config = config !== undefined ? await convertChartSpec(config) : await this.generateDefaultConfig();
        if (iLayer) {
            // Adding the provided iLayer to the config
            this._config.iLayer = iLayer;
        }
    }
}
