import { getDefaultPieChartTick } from "@arcgis/charts-shared-utils";
import { cloneDeep } from "lodash-es";
/**
 * Gets the leader lines symbol from the chart config.
 * @param config
 */
export const getLeaderLinesSymbol = (config) => {
    for (const series of config.series) {
        if (series.ticks?.lineSymbol !== undefined) {
            return series.ticks.lineSymbol;
        }
    }
    return undefined;
};
/**
 * Sets the leader lines symbol in the chart config.
 * @param config
 * @param newLeaderLinesSymbol
 */
export const setLeaderLinesSymbol = (newLeaderLinesSymbol, config) => {
    for (const series of config.series) {
        if (series.ticks === undefined || newLeaderLinesSymbol === undefined) {
            series.ticks = getDefaultPieChartTick();
        }
        if (newLeaderLinesSymbol !== undefined) {
            series.ticks.lineSymbol = cloneDeep(newLeaderLinesSymbol);
        }
    }
};
