import { RESTSymbolType } from "@arcgis/charts-spec";
import { pieChartSpecialSlicesId, getDefaultNumberFormat } from "@arcgis/charts-shared-utils";
import { PieChartSlicesDisplay } from "../../interfaces/common";
export const getOriginalName = (sliceIndex, config) => config.series?.[0]?.slices?.[sliceIndex]?.originalLabel;
export const getName = (sliceIndex, config) => config.series?.[0]?.slices?.[sliceIndex]?.label;
export const setName = (newName, sliceIndex, config) => {
    if (config.series?.[0]?.slices?.[sliceIndex]) {
        config.series[0].slices[sliceIndex].label = newName;
    }
};
export const getColor = (sliceIndex, config) => config.series?.[0]?.slices?.[sliceIndex]?.fillSymbol?.color;
export const setColor = (newColor, sliceIndex, config) => {
    const fillSymbol = config.series?.[0]?.slices?.[sliceIndex]?.fillSymbol;
    if (fillSymbol !== undefined) {
        fillSymbol.color = newColor;
        if (fillSymbol.outline !== undefined) {
            fillSymbol.outline.color = newColor;
        }
        else {
            fillSymbol.outline = { type: RESTSymbolType.SLS, color: newColor };
        }
    }
    else if (config.series?.[0]?.slices?.[sliceIndex] !== undefined) {
        config.series[0].slices[sliceIndex].fillSymbol = {
            type: RESTSymbolType.SFS,
            color: newColor,
            outline: { type: RESTSymbolType.SLS, color: newColor },
        };
    }
};
export const getOrderOfSlices = (config) => config.series?.[0]?.slices?.map((s) => s.sliceId);
export const setOrderOfSlices = (newSliceOrder, config) => {
    config.series?.[0]?.slices?.sort((prev, curr) => newSliceOrder.findIndex((s) => prev.sliceId === s) - newSliceOrder.findIndex((s) => curr.sliceId === s));
};
export const getInnerRadiusSize = (config) => config.series?.[0]?.innerRadius;
export const setInnerRadiusSize = (newInnerRadiusSize, config) => {
    if (config.series?.[0]) {
        config.series[0].innerRadius = newInnerRadiusSize;
    }
};
export const getSliceGrouping = (config) => config.series?.[0]?.sliceGrouping;
export const setSliceGrouping = (newSliceGrouping, config) => {
    if (config.series?.[0]) {
        config.series[0].sliceGrouping = newSliceGrouping;
    }
};
export const getGroupingThreshold = (config) => config.series?.[0]?.sliceGrouping?.percentageThreshold;
export const setGroupingThreshold = (newGroupingThreshold, config) => {
    if (config.series?.[0]?.sliceGrouping !== undefined) {
        config.series[0].sliceGrouping.percentageThreshold = newGroupingThreshold;
    }
    else {
        config.series[0].sliceGrouping = {
            sliceId: pieChartSpecialSlicesId.defaultGroupOfSlicesId,
            percentageThreshold: newGroupingThreshold,
        };
    }
};
export const getLabelCharacterLimit = (config) => config.series?.[0]?.dataLabelsCharacterLimit;
export const setLabelCharacterLimit = (newLabelCharacterLimit, config) => {
    if (config.series?.[0]) {
        config.series[0].dataLabelsCharacterLimit = newLabelCharacterLimit;
    }
};
export const getDecimalPlaces = (config) => config.series?.[0]?.numericValueFormat?.intlOptions?.minimumFractionDigits;
export const setDecimalPlaces = (newDecimalPlaces, config) => {
    // update numeric value format options based on new decimal places limit
    if (config.series?.[0]?.numericValueFormat !== undefined) {
        config.series[0].numericValueFormat.intlOptions.minimumFractionDigits = newDecimalPlaces;
        config.series[0].numericValueFormat.intlOptions.maximumFractionDigits = newDecimalPlaces;
    }
    else {
        config.series[0].numericValueFormat = {
            ...getDefaultNumberFormat(),
            intlOptions: {
                minimumFractionDigits: newDecimalPlaces,
                maximumFractionDigits: newDecimalPlaces,
            },
        };
    }
    // update percent value format options based on new decimal places limit
    if (config.series?.[0]?.percentValueFormat !== undefined) {
        config.series[0].percentValueFormat.intlOptions.minimumFractionDigits = newDecimalPlaces;
        config.series[0].percentValueFormat.intlOptions.maximumFractionDigits = newDecimalPlaces;
    }
    else {
        config.series[0].percentValueFormat = {
            ...getDefaultNumberFormat(),
            intlOptions: {
                minimumFractionDigits: newDecimalPlaces,
                maximumFractionDigits: newDecimalPlaces,
            },
        };
    }
};
export const getDisplayType = (config) => {
    let displayType;
    const pieSeries = config.series?.[0];
    if (pieSeries?.displayNumericValueOnDataLabel === true && pieSeries?.displayPercentageOnDataLabel === true) {
        displayType = PieChartSlicesDisplay.Both;
    }
    else if (pieSeries?.displayNumericValueOnDataLabel === true) {
        displayType = PieChartSlicesDisplay.Value;
    }
    else {
        displayType = PieChartSlicesDisplay.Percentage;
    }
    return displayType;
};
export const setDisplayType = (type, config) => {
    const displayNumericValue = type === PieChartSlicesDisplay.Value || type === PieChartSlicesDisplay.Both;
    const displayPercentage = type === PieChartSlicesDisplay.Percentage || type === PieChartSlicesDisplay.Both;
    if (config.series?.[0]) {
        config.series[0].displayNumericValueOnDataLabel = displayNumericValue;
        config.series[0].displayPercentageOnDataLabel = displayPercentage;
    }
    if (config.legend) {
        config.legend = {
            ...config.legend,
            displayNumericValue,
            displayPercentage,
        };
    }
};
export const getSlices = (config) => config.series?.[0]?.slices;
export const setSlices = (newSlices, config) => {
    if (config.series?.[0]) {
        config.series[0].slices = newSlices;
    }
};
export const resetSlices = (config) => {
    setSlices(undefined, config);
    setSliceGrouping({
        sliceId: pieChartSpecialSlicesId.defaultGroupOfSlicesId,
        percentageThreshold: getGroupingThreshold(config) ?? 0,
    }, config);
};
export const getSliceId = (sliceIndex, config) => config.series[0]?.slices?.[sliceIndex]?.sliceId;
export const getSliceIndex = (sliceId, config) => config.series[0]?.slices?.findIndex((s) => s.sliceId === sliceId);
export const getSlicesLength = (config) => config.series[0]?.slices?.length ?? 0;
