import { getDefaultChartText, getDefaultGaugeNeedle } from "@arcgis/charts-shared-utils";
import { WebChartTypes } from "@arcgis/charts-spec";
/**
 * Gets the visibility of the needle.
 * @param config
 */
export const getNeedleVisibility = (config) => config.axes[0].needle?.visible ?? true;
/**
 * Sets the visibility of the needle.
 * @param visible
 * @param config
 */
export const setNeedleVisibility = (visible, config) => {
    if (config.axes[0].needle === undefined) {
        config.axes[0].needle = getDefaultGaugeNeedle();
    }
    config.axes[0].needle.visible = visible;
};
