/**
 * Gets the content of the gauge inner label.
 * @param config
 */
export const getInnerLabelContent = (config) => config.axes[0].innerLabel?.content;
/**
 * Sets the content of the gauge inner label.
 * @param content
 * @param config
 */
export const setInnerLabelContent = (content, config) => {
    if (config.axes[0].innerLabel === undefined) {
        config.axes[0].innerLabel = getDefaultChartText();
    }
    if (content !== undefined) {
        config.axes[0].innerLabel.content = content;
    }
};
/**
 * Gets the visibility of the gauge axis ticks.
 * @param config
 */
export const getAxisTickVisibility = (config) => config.axes[0].ticks?.visible ?? true;
/**
 * Sets the visibility of the gauge axis ticks.
 * @param visible
 * @param config
 */
export const setAxisTickVisibility = (visible, config) => {
    if (config.axes[0].ticks === undefined) {
        config.axes[0].ticks = {
            type: WebChartTypes.GaugeAxisTick,
        };
    }
    config.axes[0].ticks.visible = visible;
};
