import { getDefaultGaugeSeries } from "@arcgis/charts-shared-utils";
import { RESTStatisticType } from "@arcgis/charts-spec";
import { getYAxisTitleLabel } from "../../shared/axes";
import { getFeatureIndex } from "./gauge-series";
/**
 * Updates the query object based on the provided field and aggregation type.
 * @param config
 * @param field
 * @param aggregationType
 */
const updateQuery = (props) => {
    const { config, field, aggregationType } = props;
    const featureIndex = getFeatureIndex(config);
    const query = isValidFeatureIndex(featureIndex)
        ? {}
        : {
            outStatistics: [
                {
                    statisticType: aggregationType,
                    onStatisticField: field,
                    outStatisticFieldName: `${aggregationType}_${field}`,
                },
            ],
        };
    config.series[0].query = query;
};
/**
 * Updates the title of the gauge chart based on the provided field and aggregation type.
 * @param config
 * @param layerInfo
 * @param field
 */
const updateTitle = async (props) => {
    const { config, layerInfo, field, aggregationType } = props;
    if (config.title) {
        config.title.content.text = await getYAxisTitleLabel({
            config,
            layerInfo,
            numericFields: [field],
            aggregationType,
        });
    }
};
export const updateSeries = async (props) => {
    const { config, field, aggregationType } = props;
    if (config.series[0] === undefined) {
        config.series = [getDefaultGaugeSeries({})];
    }
    // update the field
    const isValid = isValidFeatureIndex(config.series[0]?.featureIndex);
    let updatedField = isValid ? field : `${aggregationType}_${field}`;
    // NOTE: if the field is already prefixed with the aggregation type, then remove it for the `Gauge by Feature` type
    if (isValid && field.startsWith(`${aggregationType}_`)) {
        updatedField = field.replace(`${aggregationType}_`, "");
    }
    config.series[0].x = updatedField;
    updateQuery({ config, field, aggregationType });
    await updateTitle(props);
};
/**
 * Gets the aggregation type from the config.
 * @param config - the guage chart config
 */
export const getAggregationType = (config) => config.series[0]?.query?.outStatistics?.[0]?.statisticType ?? RESTStatisticType.Count;
/**
 * Sets the aggregation type to the config.
 * @param newAggregationType
 * @param config
 * @param layerInfo
 */
export const setAggregationType = async (newAggregationType, config, layerInfo) => {
    const field = getField(config);
    await updateSeries({
        config,
        layerInfo,
        field,
        aggregationType: newAggregationType,
    });
};
/**
 * Checks if the feature index is valid.
 *
 * NOTE: It should be a number and greater than or equal to 0.
 * @param { number | undefined } featureIndex - the feature index
 *
 * @returns boolean
 */
export const isValidFeatureIndex = (featureIndex) => typeof featureIndex === "number" && featureIndex >= 0;
/**
 * get the field from the config
 * @param config - the guage chart config
 */
export const getField = (config) => isValidFeatureIndex(config.series[0]?.featureIndex)
    ? config.series[0]?.x
    : (config.series[0]?.query?.outStatistics?.[0]?.onStatisticField ?? "");
/**
 * set the new field to the config
 * @param newField - the new field
 * @param config - the guage chart config
 */
export const setField = async (newField, config, layerInfo) => {
    const aggregationType = getAggregationType(config);
    await updateSeries({ config, layerInfo, field: newField, aggregationType });
};
/**
 * get the inner radius from the config
 * @param config - the guage chart config
 */
export const getInnerRadius = (config) => config.innerRadius ?? 50;
/**
 * set the new inner radius to the config
 * @param newInnerRadius - the new inner radius
 * @param config - the guage chart config
 */
export const setInnerRadius = (newInnerRadius, config) => {
    config.innerRadius = newInnerRadius;
};
/**
 * get the start angle from the config
 * @param config - the guage chart config
 */
export const getStartAngle = (config) => config.startAngle ?? -90;
/**
 * set the new start angle to the config
 * @param newStartAngle - the new start angle
 * @param config - the guage chart config
 */
export const setStartAngle = (newStartAngle, config) => {
    config.startAngle = newStartAngle;
};
/**
 * get the end angle from the config
 * @param config - the guage chart config
 */
export const getEndAngle = (config) => config.endAngle ?? 270;
/**
 * set the new end angle to the config
 * @param newEndAngle - the new end angle
 * @param config - the guage chart config
 */
export const setEndAngle = (newEndAngle, config) => {
    config.endAngle = newEndAngle;
};
