import { getDefaultLineChartLineSymbol } from "@arcgis/charts-shared-utils";
export const getLineStyleFromSeries = (series) => series.lineSymbol?.style;
export const setLineStyleToSeries = (newStyle, series) => {
    series.lineSymbol ??= getDefaultLineChartLineSymbol();
    series.lineSymbol.style = newStyle;
};
export const getLineWidthFromSeries = (series) => series.lineSymbol?.width;
export const setLineWidthToSeries = (newWidth, series) => {
    series.lineSymbol ??= getDefaultLineChartLineSymbol();
    series.lineSymbol.width = newWidth;
};
