export const getHideOversizedStackedLabels = (seriesIndex, config) => config.series?.[seriesIndex]?.hideOversizedStackedLabels;
export const setHideOversizedStackedLabels = (newValue, seriesIndex, config) => {
    if (config.series?.[seriesIndex] !== undefined) {
        config.series[seriesIndex].hideOversizedStackedLabels = newValue;
    }
};
