/**
 * Gets the color match.
 *
 * @param config the chart configuration
 */
export const getColorMatch = (config) => config.colorMatch ?? true;
/**
 * Sets the color match by updating the config accordingly.
 *
 * @param newColorMatch the new color match value
 * @param config the chart configuration
 */
export const setColorMatch = (newColorMatch, config) => {
    config.colorMatch = newColorMatch;
};
