import { WebChartTypes } from "@arcgis/charts-spec";
import { isModelOfType } from "./type-guards";
import { BarChartModel } from "../bar-chart-model/bar-chart-model";
import { BoxPlotModel } from "../box-plot-model/box-plot-model";
import { HistogramModel } from "../histogram-model/histogram-model";
import { LineChartModel } from "../line-chart-model/line-chart-model";
import { PieChartModel } from "../pie-chart-model/pie-chart-model";
import { ScatterPlotModel } from "../scatter-plot-model/scatter-plot-model";
import { GaugeModel } from "../gauge-model/gauge-model";
/**
 * Creates chart model to use for config interaction.
 * @param props.layer The layer to use for the model.
 * @param props.chartType The chart type to create.
 * @param props.config Optional starting config for the model.
 */
export async function createModel(props) {
    const { config, layer } = props;
    const type = props.chartType;
    let model;
    switch (type) {
        case WebChartTypes.BarSeries: {
            model = new BarChartModel();
            break;
        }
        case WebChartTypes.LineSeries: {
            model = new LineChartModel();
            break;
        }
        case WebChartTypes.PieSeries: {
            model = new PieChartModel();
            break;
        }
        case WebChartTypes.HistogramSeries: {
            model = new HistogramModel();
            break;
        }
        case WebChartTypes.ScatterSeries: {
            model = new ScatterPlotModel();
            break;
        }
        case WebChartTypes.BoxPlotSeries: {
            model = new BoxPlotModel();
            break;
        }
        case WebChartTypes.GaugeSeries: {
            model = new GaugeModel();
            break;
        }
        default: {
            model = undefined;
            break;
        }
    }
    // Run setup for the model
    await model?.setup({
        layer,
        config,
    });
    if (type !== undefined && isModelOfType(model, type)) {
        return model;
    }
    else {
        return undefined;
    }
}
