import { TextElements, getDefaultChartText, getDefaultFont } from "@arcgis/charts-shared-utils";
import { cloneDeep } from "lodash-es";
/**
 * Gets the data labels visibility from the chart config.
 * @param config
 */
export const getDataLabelsVisibility = (config) => config.series[0]?.dataLabels?.visible ?? false;
/**
 * Sets the data labels visibility in the chart config.
 * @param config
 * @param newDataLabelsVisibility
 */
export const setDataLabelsVisibility = (newDataLabelsVisibility, config) => {
    config.series.forEach((series) => {
        if (series.dataLabels !== undefined) {
            series.dataLabels.visible = newDataLabelsVisibility;
        }
        else {
            series.dataLabels = getDefaultChartText({
                text: "",
                visible: newDataLabelsVisibility,
                font: getDefaultFont(TextElements.DataLabels),
                type: TextElements.DataLabels,
            });
        }
    });
};
/**
 * Gets the data labels text symbol from the chart config.
 * @param config
 */
export const getDataLabelsSymbol = (config) => {
    for (const series of config.series) {
        if (series.dataLabels !== undefined) {
            return series.dataLabels.content;
        }
    }
    return undefined;
};
/**
 * Sets the data labels text symbol in the chart config.
 * @param config
 * @param newDataLabelsSymbol
 */
export const setDataLabelsSymbol = (newDataLabelsSymbol, config) => {
    for (const series of config.series) {
        if (series.dataLabels === undefined || newDataLabelsSymbol === undefined) {
            series.dataLabels = getDefaultChartText({
                text: "",
                visible: true,
                font: getDefaultFont(TextElements.DataLabels),
                type: TextElements.DataLabels,
            });
        }
        if (newDataLabelsSymbol !== undefined) {
            series.dataLabels.content = cloneDeep(newDataLabelsSymbol);
        }
    }
};
