/**
 * set the mean color in the config
 * @param newMeanColor - the new mean color
 * @param config - the histogram config
 */
export const setMeanColor = (newMeanColor, config) => {
    // creating the default `overlays` and `mean` if they are missing
    config.series[0].overlays ??= getDefaultHistogramOverlays();
    config.series[0].overlays.mean ??= getDefaultOverlay(1);
    config.series[0].overlays.mean.symbol = {
        ...config.series[0].overlays.mean.symbol,
        ...newMeanColor,
    };
};
