import { isEmpty } from "lodash-es";
import { formatMessage, getDefaultScatterPlotSizePolicy, getDefaultTrendLine, getFieldAlias, isIntegerField, } from "@arcgis/charts-shared-utils";
import { fetchCommonStrings } from "../../t9n/common";
import { setMinXBound, setMaxXBound, setMinYBound, setMaxYBound, setIntegerOnlyValuesInAxis, updateAxisTitle, } from "../../shared/axes";
/**
 * reset x and y axes bounds
 * @param config - scatter plot config
 */
const resetAxesBounds = (config) => {
    // reset x-axis bounds
    setMinXBound(config, null);
    setMaxXBound(config, null);
    // reset y-axis bounds
    setMinYBound(config, null);
    setMaxYBound(config, null);
};
/**
 * set the axes `integerOnlyValues` option for axes
 * @param props.name - the axis title text
 * @param props.axisIndex - the axis index - 0 for x-axis, 1 for y-axis
 * @param props.config - the scatter plot config
 * @param props.layerInfo - the layer info
 */
const setAxesIntegerOnly = (props) => {
    const { name, axisIndex, config, layerInfo } = props;
    const { layerFieldsInfo } = layerInfo;
    // Finding the field info based on its name
    const iField = layerFieldsInfo.find((field) => field.name === name);
    // If the field is an Integer, we set the axis `integerOnlyValues` to true to avoid float values to be displayed
    const integerOnlyValues = iField !== undefined && isIntegerField(iField);
    // update the axis with the integer only values
    setIntegerOnlyValuesInAxis(config, axisIndex, integerOnlyValues);
};
/**
 * set the titles for the scatter plot chart
 * @param props.x - the x axis title text
 * @param props.y - the y axis title text
 * @param props.config - the scatter plot config
 * @param props.layerInfo - the layer info
 */
const setTitles = async (props) => {
    const { x, y, config, layerInfo } = props;
    const { layerFieldsInfo } = layerInfo;
    // get the x and y field aliases
    const xAlias = getFieldAlias(layerFieldsInfo, x);
    const yAlias = getFieldAlias(layerFieldsInfo, y);
    // set the title text for x-axis
    if (!isEmpty(x)) {
        // update the X-axis title
        updateAxisTitle(config, 0, xAlias);
    }
    // set the title text for y-axis
    if (!isEmpty(y)) {
        updateAxisTitle(config, 1, yAlias);
    }
    // set the chart title text
    if (!isEmpty(x) && !isEmpty(y)) {
        const commonStrings = await fetchCommonStrings();
        const chartTitle = formatMessage(commonStrings.titleLabels.xAndY, { x: xAlias, y: yAlias });
        if (config.title?.content) {
            config.title.content.text = chartTitle;
        }
    }
};
/**
 * get the x-axis field from the config
 * @param config - the scatter plot config
 */
export const getXAxisField = (config) => config.series[0]?.x ?? "";
/**
 * get the y-axis field from the config
 * @param config - the scatter plot config
 */
export const getYAxisField = (config) => config.series[0]?.y ?? "";
/**
 * set the x-axis field name in the config
 * @param newXAxisField - the x-axis field name
 * @param config - the scatter plot config
 * @param layerInfo - the layer info
 */
export const setXAxisField = async (newXAxisField, config, layerInfo) => {
    // reset the axes bounds given changing the x field could change the axes range
    resetAxesBounds(config);
    // setting series x
    if (config.series[0]) {
        config.series[0].x = newXAxisField;
    }
    await setTitles({
        x: newXAxisField,
        y: getYAxisField(config),
        config,
        layerInfo,
    });
    // updating the 'integer only' option for x-axis
    setAxesIntegerOnly({
        name: newXAxisField,
        axisIndex: 0,
        config,
        layerInfo,
    });
};
/**
 * set the y-axis field in the config
 * @param newYAxisField - the y-axis field name
 * @param config - the scatter plot config
 * @param layerInfo - the layer info
 */
export const setYAxisField = async (newYAxisField, config, layerInfo) => {
    // reset the axes bounds given changing the y field could change the axes range
    resetAxesBounds(config);
    // setting series y
    if (config.series[0]) {
        config.series[0].y = newYAxisField;
    }
    await setTitles({
        x: getXAxisField(config),
        y: newYAxisField,
        config,
        layerInfo,
    });
    // updating the 'integer only' option for y-axis
    setAxesIntegerOnly({
        name: newYAxisField,
        axisIndex: 1,
        config,
        layerInfo,
    });
};
/**
 * get fixed marker symbol from the config
 * @param config - the scatter plot config
 */
export const getFixedMarkerSymbol = (config) => {
    const defaultSimpleMarkerSymbol = {};
    return config.series[0]?.markerSymbol ?? defaultSimpleMarkerSymbol;
};
/**
 * set the fixed marker symbol in the config
 * @param newFixedMarkerSymbol - the fixed marker symbol
 * @param config - the scatter plot config
 */
export const setFixedMarkerSymbol = (newFixedMarkerSymbol, config) => {
    if (config.series[0]) {
        config.series[0].markerSymbol = newFixedMarkerSymbol;
    }
};
/**
 * get the show linear trend option from the config
 * @param config - the scatter plot config
 */
export const getShowLinearTrend = (config) => (config.series[0]?.overlays?.trendLine?.visible && config.series[0]?.overlays?.trendLine?.created) ?? false;
/**
 * get linear trend symbol from the config
 * @param config - the scatter plot config
 */
export const getLinearTrendSymbol = (config) => {
    const defaultSimpleLineSymbol = {};
    return config.series[0]?.overlays?.trendLine?.symbol ?? defaultSimpleLineSymbol;
};
/**
 * set the linear trend option in the config
 * @param newLinearTrendSymbol - the linear trend symbol
 * @param config - the scatter plot config
 */
export const setLinearTrendSymbol = (newLinearTrendSymbol, config) => {
    config.series[0].overlays ??= getDefaultTrendLine();
    config.series[0].overlays.trendLine ??= getDefaultTrendLine().trendLine;
    config.series[0].overlays.trendLine.symbol = newLinearTrendSymbol;
};
/**
 * Set the visibility of the linear trend line in the scatter plot config
 * @param newVisibility - the new visibility of the linear trend line
 * @param config - the scatter plot config
 */
export const setLinearTrendLineVisibility = (newVisibility, config) => {
    config.series[0].overlays ??= getDefaultTrendLine();
    config.series[0].overlays.trendLine ??= getDefaultTrendLine().trendLine;
    config.series[0].overlays.trendLine.visible = newVisibility;
};
/**
 * set show linear trend in the config
 * @param newShowLinearTrend - determines whether to show linear trend line or not
 * @param config - the scatter plot config
 */
export const setShowLinearTrend = (newShowLinearTrend, config) => {
    const isTrendLineCreated = config.series[0]?.overlays?.trendLine?.created ?? false;
    // set the trend line created to 'true' if the trend line is not created and the new show linear trend is true
    if (!isTrendLineCreated && newShowLinearTrend) {
        config.series[0].overlays ??= getDefaultTrendLine();
        config.series[0].overlays.trendLine ??= getDefaultTrendLine().trendLine;
        config.series[0].overlays.trendLine.created = newShowLinearTrend;
    }
    // set the visibility of the trend line
    setLinearTrendLineVisibility(newShowLinearTrend, config);
};
/**
 * get the size policy from the config
 * @param config - the scatter plot config
 */
export const getSizePolicy = (config) => config.series[0]?.sizePolicy ?? getDefaultScatterPlotSizePolicy();
/**
 * set the size policy in the config
 * @param newSizePolicy - the new size policy
 * @param config - the scatter plot config
 */
export const setSizePolicy = (newSizePolicy, config) => {
    if (config.series[0]) {
        if (newSizePolicy !== undefined && newSizePolicy.field?.trim() !== "") {
            config.series[0].sizePolicy = { ...config.series[0].sizePolicy, ...newSizePolicy };
        }
        else {
            config.series[0].sizePolicy = undefined;
        }
    }
};
/**
 * Get the chart overlays. The scatter plot implements only 1 overlay, **trendLine**.
 * @param {ScatterPlotConfig} config - the scatter plot config
 */
export const getSeriesOverlays = (config) => config?.series?.[0]?.overlays;
/**
 * Set the chart overlays. The scatter plot implements only 1 overlay, **trendLine**.
 * @param {ScatterPlotOverlays} newOverlays - the new overlays
 * @param {ScatterPlotConfig} config - the scatter plot config
 */
export const setSeriesOverlays = (newOverlays, config) => {
    if (config?.series[0] !== undefined) {
        config.series[0].overlays = newOverlays;
    }
};
