import { ChartSubType, getChartSubType, isPartialConfig, } from "@arcgis/charts-shared-utils";
import { ChartModel } from "../chart-model/chart-model";
import { getOrderOfSeries, getSeriesColor, getSeriesName, setOrderOfSeries, setSeriesColor, setSeriesName, getStackedType, setStackedType, } from "./utils/serial-chart-series";
import { getOrderByList, getXAxisField, setSortOrderKind } from "../shared/data";
import { buildNewSeries, getAggregationType, getCustomSortValues, getDataFilter, getNumericFields, getSortOrderKind, getSplitByField, setAggregationType, setDataFilter, setNumericFields, setOrderByList, setSplitByField, setXAxisField, } from "./utils/serial-chart-data";
import { isValidNumericFields, isValidSplitByField } from "../shared/utils";
import { SerialChartDataSortingKinds } from "../interfaces/common";
export class SerialChartModel extends ChartModel {
    // ---------------------------------------------------------------------------------------------
    // Data Props
    // ---------------------------------------------------------------------------------------------
    /**
     * Gets the x-axis field for the chart.
     * @returns {string}
     */
    getXAxisField() {
        return getXAxisField(this._config);
    }
    /**
     * Sets the x-axis field for the chart.
     * @param {string} newXAxisField The new x-axis field.
     */
    async setXAxisField(newXAxisField) {
        // check if the new x-axis field is not empty
        const isNewXAxisFieldEmpty = newXAxisField.trim() === "";
        if (!isNewXAxisFieldEmpty) {
            await setXAxisField(newXAxisField, this._config, this.layerInfo);
        }
        // get numeric fields and chart subtype
        const numericFields = getNumericFields(this._config);
        const subType = getChartSubType(this._config.series);
        // determine if we need to reset the config
        const isBarAndLineFromFields = subType === ChartSubType.BarAndLineFromFields;
        const containsObjectIdField = numericFields.includes(this.layerInfo?.objectIdField ?? "");
        const resetConfig = isNewXAxisFieldEmpty || (isBarAndLineFromFields && containsObjectIdField);
        if (resetConfig) {
            await this.resetConfig();
            const numericFieldsContainFID = numericFields.length === 1 && numericFields[0] === this.layerInfo?.objectIdField;
            // re-add numeric fields if
            // - the numeric fields contain the FID field
            // - the numeric fields are not empty
            // - the chart subtype is `ChartSubType.BarAndLineFromFields`
            if (this.layerInfo && numericFields.length > 0 && !numericFieldsContainFID) {
                await setNumericFields(numericFields, this._config, this.layerInfo);
            }
        }
        this.dispatchEvent(new CustomEvent("arcgisChartsModelDataChange"));
        this.emitConfigUpdate("setXAxisField");
    }
    /**
     * Gets the aggregation type for the bar chart.
     * @returns {"count" | "sum" | "min" | "max" | "avg" | "stddev" | "var" | "percentile_cont" | "percentile_disc" | "envelope-aggregate" | "centroid-aggregate" | "convex-hull-aggregate" | "no_aggregation"}
     */
    getAggregationType() {
        return getAggregationType(this._config);
    }
    /**
     * Sets the aggregation type for the chart.
     * @param {"count" | "sum" | "min" | "max" | "avg" | "stddev" | "var" | "percentile_cont" | "percentile_disc" | "envelope-aggregate" | "centroid-aggregate" | "convex-hull-aggregate" | "no_aggregation"} newAggregationType The new aggregation type. Must be one of the values allowed by `RESTStatisticType`.
     * @async **This method is `async` and must be awaited for the changes to take effect.**
     */
    async setAggregationType(newAggregationType) {
        if (this.layerInfo) {
            await setAggregationType(newAggregationType, this._config, this.layerInfo);
            this.dispatchEvent(new CustomEvent("arcgisChartsModelDataChange"));
            this.emitConfigUpdate("setAggregationType");
        }
    }
    /**
     * Gets the split-by field for the chart.
     * @returns {string}
     */
    getSplitByField() {
        return getSplitByField(this._config);
    }
    /**
     * Sets the split-by field for the chart.
     * @param {string} newSplitByField The new split-by field.
     * @async **This method is `async` and must be awaited for the changes to take effect.**
     */
    async setSplitByField(newSplitByField) {
        if (this.layerInfo?.layerFieldsInfo && isValidSplitByField(this.layerInfo?.layerFieldsInfo, newSplitByField)) {
            await setSplitByField(newSplitByField, this._config, this.layerInfo);
            this.dispatchEvent(new CustomEvent("arcgisChartsModelDataChange"));
            this.emitConfigUpdate("setSplitByField");
        }
    }
    /**
     * Gets the numeric fields array for the chart.
     * @returns {string[]}
     */
    getNumericFields() {
        return getNumericFields(this._config);
    }
    /**
     * Sets the numeric field or numeric fields array for the chart.
     * @param {string | string[]} newNumericFields The new numeric field string or numeric fields array.
     * @async **This method is `async` and must be awaited for the changes to take effect.**
     */
    async setNumericFields(newNumericFields) {
        // The function requires a string array but the user can input either
        if (typeof newNumericFields === "string") {
            newNumericFields = [newNumericFields];
        }
        const valid = isValidNumericFields(this.layerInfo?.layerFieldsInfo ?? [], newNumericFields);
        if (valid && this.layerInfo) {
            await setNumericFields(newNumericFields, this._config, this.layerInfo);
            // reset the config if the numericFields contains OID and chart sub type is `ChartSubType.BarAndLineFromFields`
            const numericFields = getNumericFields(this._config);
            const subType = getChartSubType(this._config.series);
            const resetConfig = subType === ChartSubType.BarAndLineFromFields &&
                numericFields.some((field) => field === this.layerInfo?.objectIdField);
            if (resetConfig) {
                await this.resetConfig();
            }
            this.dispatchEvent(new CustomEvent("arcgisChartsModelDataChange"));
            this.emitConfigUpdate("setNumericFields");
        }
    }
    /**
     * Gets the data filter for the bar chart.
     * @returns {WebChartDataFilters | undefined}
     */
    getDataFilter() {
        return getDataFilter(this._config);
    }
    /**
     * Sets the data filter for the bar chart.
     * @param {WebChartDataFilters | undefined} newDataFilter The new data filter.
     */
    async setDataFilter(newDataFilter) {
        setDataFilter(newDataFilter, this._config);
        //  update sort order by list when the data filters get updated
        const sortOrder = getSortOrderKind(this._config);
        if (sortOrder === SerialChartDataSortingKinds.customSort) {
            const orderByList = await getCustomSortValues(this._config, this.layerInfo);
            setSortOrderKind(sortOrder, this._config, orderByList);
        }
        this.dispatchEvent(new CustomEvent("arcgisChartsModelDataChange"));
        this.emitConfigUpdate("setDataFilter");
    }
    /**
     * Gets the list of the category values for the chart.
     * @returns {string[]}
     */
    async getCustomSortValues() {
        return await getCustomSortValues(this._config, this.layerInfo);
    }
    /**
     * Gets the sort order for the bar chart.
     * @returns {"xAxisAsc" | "xAxisDesc" | "yAxisAsc" | "yAxisDesc" | "customSort"}
     */
    getSortOrder() {
        return getSortOrderKind(this._config);
    }
    /**
     * Sets the sort order for the bar chart.
     * @param {"xAxisAsc" | "xAxisDesc" | "yAxisAsc" | "yAxisDesc"}  newSortOrder The new sort order.
     * @param {string[]} orderByList The list of category's values to order by.
     */
    setSortOrder(newSortOrder, orderByList) {
        setSortOrderKind(newSortOrder, this._config, orderByList);
        this.emitConfigUpdate("setSortOrder");
    }
    /**
     * Gets the list of custom sort values for custom sort
     * @returns {string[]}
     */
    getOrderByList() {
        return getOrderByList(this._config);
    }
    /**
     * Sets the list of custom sort values for custom sort
     * @param {string[]} newOrderByList The new list of custom sort values
     */
    setOrderByList(newOrderByList) {
        const updateOrderByList = !isPartialConfig(this._config);
        if (updateOrderByList) {
            setOrderByList(newOrderByList, this._config);
            this.emitConfigUpdate("setOrderByList");
        }
    }
    /**
     * Updates the series based on the layer filter.
     * @async **This method is `async` and must be awaited for the changes to take effect.**
     */
    async updateSeriesFromLayerFilter() {
        if (this.layerInfo !== undefined) {
            await buildNewSeries({
                splitByField: this.getSplitByField(),
                numericFields: this.getNumericFields(),
                aggregationType: this.getAggregationType(),
                config: this._config,
                layerInfo: this.layerInfo,
            });
            this.dispatchEvent(new CustomEvent("arcgisChartsModelDataChange"));
            this.emitConfigUpdate("updateSeriesFromLayerFilter");
        }
    }
    // ---------------------------------------------------------------------------------------------
    // Series Props
    // ---------------------------------------------------------------------------------------------
    /**
     * Get the name of the series based on the series index.
     * If the index is out of bounds, it will return `undefined`.
     * @param {number} seriesIndex The index of the series.
     * @returns {string | undefined}
     */
    getSeriesName(seriesIndex) {
        return getSeriesName(seriesIndex, this._config);
    }
    /**
     * Set name of the series based on the series index.
     * @param {string} newName The new name for the series.
     * @param {number} seriesIndex The index of the series.
     */
    setSeriesName(newName, seriesIndex) {
        setSeriesName(newName, seriesIndex, this._config);
        this.emitConfigUpdate("setSeriesName");
    }
    /**
     * Gets the color of the series based on the series index. If the index is out of bounds, it will return `undefined`.
     * @param {number} seriesIndex The index of the series.
     * @returns {Color | undefined}
     */
    getSeriesColor(seriesIndex) {
        return getSeriesColor(seriesIndex, this._config);
    }
    /**
     * Set the color of the series based on the series index.
     * @param {Color | undefined} newColor The new color for the series.
     * @param {number} seriesIndex The index of the series.
     */
    setSeriesColor(newColor, seriesIndex) {
        setSeriesColor(newColor, seriesIndex, this._config);
        this.emitConfigUpdate("setSeriesColor");
    }
    /**
     * Get the order of the series array by returning an array of series ids.
     * @returns {string[]}
     */
    getOrderOfSeries() {
        return getOrderOfSeries(this._config);
    }
    /**
     * Set the order of the series array by providing an array of series ids.
     * @param {string[]} newOrder The new order of the series array.
     */
    setOrderOfSeries(newOrder) {
        setOrderOfSeries(newOrder, this._config);
        this.emitConfigUpdate("setOrderOfSeries");
    }
    /**
     * Get how the bars/lines should be placed when multiple series are rendered in the same chart, say when "split by" operation is executed on any field or
     * when multiple attributes/fields corresponding to the same set of classes are to be visualized.
     *
     * Currently this property is only used for bar and line series.
     *
     * @default sideBySide
     *
     * @returns {WebChartStackedKinds | undefined}
     */
    getStackedType() {
        return getStackedType(this._config);
    }
    /**
     * Set how the bars/lines should be placed when multiple series are rendered in the same chart, say when "split by" operation is executed on any field or
     * when multiple attributes/fields corresponding to the same set of classes are to be visualized.
     *
     * Currently this property is only used for bar and line series.
     *
     * @param {WebChartStackedKinds | undefined} newStackedType The new stacked type.
     *
     * @default sideBySide
     */
    async setStackedType(newStackedType) {
        await setStackedType({
            newStackedType,
            config: this._config,
            layerInfo: this.layerInfo,
        });
        this.emitConfigUpdate("setStackedType");
    }
    /**
     * Moves a series from one index in the series array to another.
     * @param {number} fromIndex Index to move the series from.
     * @param {number} toIndex Index to move the series to.
     */
    moveSeries(fromIndex, toIndex) {
        const newOrder = this.getOrderOfSeries();
        const [seriesToMove] = newOrder.splice(fromIndex, 1);
        newOrder.splice(toIndex, 0, seriesToMove);
        this.setOrderOfSeries(newOrder);
        this.emitConfigUpdate("moveSeries");
    }
    /**
     * Move the custom sort values in-place for the order by list in the config.
     *
     * NOTE: This method should be only used when the sort order is set to `SerialChartDataSortingKinds.customSort`.
     * @param {number} fromIndex The index to move the custom sort value from.
     * @param {number} toIndex The index to move the custom sort value to.
     */
    moveCustomSortValuesInOrderByList(fromIndex, toIndex) {
        const orderByList = getOrderByList(this._config);
        const [valueToMove] = orderByList.splice(fromIndex, 1);
        orderByList.splice(toIndex, 0, valueToMove);
        setOrderByList(orderByList, this._config);
        this.emitConfigUpdate("moveCustomSortValuesInOrderByList");
    }
    // ---------------------------------------------------------------------------------------------
    // Protected methods
    // ---------------------------------------------------------------------------------------------
    /**
     * reset the config to the default config
     */
    async resetConfig() {
        const oldConfigId = this._config.id;
        this._config = await this.generateDefaultConfig();
        this._config.id = oldConfigId;
        this.dispatchEvent(new CustomEvent("arcgisChartsModelDataChange"));
        this.emitConfigUpdate("resetConfig");
    }
}
