import { RESTStatisticType, WebChartStackedKinds } from "@arcgis/charts-spec";
import { TextElements, formatMessage, getDefaultAggregationTypeLabel, getDefaultAxes, getDefaultChartText, getDefaultFont, getStackedType, getDefaultCategoryFormat, getFieldAlias, isNumberFormatOptions, } from "@arcgis/charts-shared-utils";
import { fetchCommonStrings } from "../t9n/common";
/**
 * Updates the axis config with the provided title text. Provides a default title object if one does not exist.
 * @param axisConfig - the axis configuration
 * @param title - the title text
 * @param axisIndex - the index of the axis
 */
export function updateAxisTitle(config, axisIndex, title) {
    const axisConfig = config?.axes?.[axisIndex];
    if (axisConfig?.title !== undefined) {
        axisConfig.title.content.text = title;
    }
    else if (axisConfig !== undefined) {
        axisConfig.title = getDefaultChartText({
            text: title,
            visible: true,
            font: getDefaultFont(TextElements.AxisTitle),
        });
    }
}
/**
 * Gets the proper Y-axis title label based on the chart configuration.
 */
export async function getYAxisTitleLabel(props) {
    const { config, layerInfo, numericFields, aggregationType } = props;
    let yAxisTitleLabel = "";
    if (config) {
        // determine if it's a stacked100 bar type
        const isStacked100 = getStackedType(config) === WebChartStackedKinds.Stacked100;
        const commonStrings = await fetchCommonStrings();
        if (isStacked100) {
            // No matter what the title is "Percent" in stacked 100% mode
            yAxisTitleLabel = commonStrings.axisLabels.percent;
        }
        else {
            const aggregation = await getDefaultAggregationTypeLabel(aggregationType);
            if (numericFields.length === 1) {
                const fieldAlias = getFieldAlias(layerInfo.layerFieldsInfo, numericFields[0]);
                if (aggregationType === RESTStatisticType.NoAggregation) {
                    yAxisTitleLabel = fieldAlias;
                }
                else if (aggregationType === RESTStatisticType.Count && numericFields[0] === layerInfo.objectIdField) {
                    yAxisTitleLabel = commonStrings.axisLabels.count;
                }
                else {
                    yAxisTitleLabel = formatMessage(commonStrings.axisLabels.aggregationOfFieldName, {
                        aggregation,
                        fieldName: fieldAlias,
                    });
                }
            }
            else if (numericFields.length > 1) {
                if (aggregationType === RESTStatisticType.NoAggregation) {
                    yAxisTitleLabel = commonStrings.axisLabels.values;
                }
                else {
                    yAxisTitleLabel = formatMessage(commonStrings.axisLabels.aggregationOfValues, {
                        aggregation,
                    });
                }
            }
        }
    }
    return yAxisTitleLabel;
}
/**
 * Get the axes of the chart
 * @param config - the config object
 */
export const getAxes = (config) => 
// Ensure the result is always an array of WebChartAxis, filtering out any undefined values
config?.axes?.filter((axis) => axis !== undefined);
export const getAxis = async (config, axisIndex) => config.axes?.[axisIndex] ?? (await getDefaultAxes(config.type))[axisIndex];
export const setAxis = (config, axis, axisIndex) => {
    // update the axis only if 'axes' is defined in the config
    if (config.axes !== undefined) {
        config.axes[axisIndex] = axis;
    }
};
/**
 * Get the maximum or minimum bound of the axis
 * @param config - the config object
 * @param axisIndex - the index of the axis
 * @param isMax - whether it is the max bound or not
 */
export const getAxisBound = (config, axisIndex, isMax) => {
    let axisBound = Number.NaN;
    if (isMax) {
        axisBound = config.axes?.[axisIndex]?.maximum;
    }
    else {
        axisBound = config.axes?.[axisIndex]?.minimum;
    }
    return axisBound ?? Number.NaN;
};
/**
 * Set the axis bound
 * @param config - the config object
 * @param value - the value to set
 * @param axisIndex - the index of the axis
 * @param isMax - whether it is the max bound
 */
export const setAxisBound = async (config, value, axisIndex, isMax) => {
    const axis = config?.axes?.[axisIndex];
    if (isMax && axis !== undefined) {
        axis.maximum = value;
    }
    else if (!isMax && axis !== undefined) {
        axis.minimum = value;
    }
};
/**
 * Get the maximum bound for the y-axis
 * @param config - the config object
 */
export const getMaxYBound = (config) => getAxisBound(config, 1, true);
/**
 * Set the maximum Y bound
 * @param config - the config object
 * @param newMaxYBound - the value to set
 */
export const setMaxYBound = async (config, newMaxYBound) => {
    await setAxisBound(config, newMaxYBound, 1, true);
};
/**
 * Get the minimum bound for the y-axis
 * @param config - the config object
 */
export const getMinYBound = (config) => getAxisBound(config, 1, false);
/**
 * Set the minimum Y bound
 * @param config - the config object
 * @param newMinYBound - the value to set
 */
export const setMinYBound = async (config, newMinYBound) => {
    await setAxisBound(config, newMinYBound, 1, false);
};
/**
 * Get the maximum bound for the x-axis
 * @param config - the config object
 */
export const getMaxXBound = (config) => getAxisBound(config, 0, true);
/**
 * Set the maximum X bound
 * @param config - the config object
 * @param newMaxXBound - the value to set
 */
export const setMaxXBound = async (config, newMaxXBound) => {
    await setAxisBound(config, newMaxXBound, 0, true);
};
/**
 * Get the minimum bound for the x-axis
 * @param config - the config object
 */
export const getMinXBound = (config) => getAxisBound(config, 0, false);
/**
 * Set the minimum X bound
 * @param config - the config object
 * @param newMinXBound - the value to set
 */
export const setMinXBound = async (config, newMinXBound) => {
    await setAxisBound(config, newMinXBound, 0, false);
};
/**
 * Get the valueFormat of the x-axis
 * @param config
 * @returns
 */
export const getXAxisValueFormat = (config) => config.axes?.[0]?.valueFormat ?? getDefaultCategoryFormat();
export const setXAxisValueFormat = async (config, valueFormat) => {
    const xAxis = await getAxis(config, 0);
    if (xAxis !== undefined && valueFormat !== undefined) {
        const updatedXAxis = {
            ...xAxis,
            ...{
                valueFormat,
            },
        };
        setAxis(config, updatedXAxis, 0);
    }
};
/**
 * Get the valueFormat of the y-axis
 * @param config - the config object
 */
export const getYAxisValueFormat = (config) => {
    let yAxisValueFormat;
    // use the valueFormat from the y-axis if it is a number format
    if (isNumberFormatOptions(config.axes?.[1]?.valueFormat)) {
        yAxisValueFormat = config.axes?.[1]?.valueFormat;
    }
    return yAxisValueFormat;
};
export const setYAxisValueFormat = async (config, valueFormat) => {
    const yAxis = await getAxis(config, 1);
    if (yAxis !== undefined && valueFormat !== undefined) {
        const updatedYAxis = {
            ...yAxis,
            ...{
                valueFormat,
            },
        };
        setAxis(config, updatedYAxis, 1);
    }
};
/**
 * Set the integer only values in the axis
 * @param config - the config object
 * @param axisIndex - the index of the axis
 * @param integerOnlyValues - the value to set
 */
export const setIntegerOnlyValuesInAxis = (config, axisIndex, integerOnlyValues) => {
    const axis = config.axes?.[axisIndex];
    if (axis !== undefined) {
        axis.integerOnlyValues = integerOnlyValues;
    }
};
