/**
 * Sorts arrayA based on the indexes of the elements in arrayB
 * @param {(number | string)[]} arrayA - The array to be sorted
 * @param {(number | string)[]} arrayB - The array to be used for sorting
 */
export function sortArrayAUsingArrayB(arrayA, arrayB) {
    let result;
    if (arrayB.length === 0) {
        // if arrayB is empty, there is no custom order to apply
        result = arrayA;
    }
    else {
        // build a lookup table of value -> desired index
        const indexMap = new Map();
        arrayB.forEach((val, i) => {
            indexMap.set(val, i);
        });
        // create a copy of arrayA and sort it
        result = arrayA.slice().sort((a, b) => {
            const aIndex = indexMap.get(a);
            const bIndex = indexMap.get(b);
            let position = 0;
            // if both are missing from the map, treat them as equal.
            if (aIndex === undefined && bIndex === undefined) {
                position = 0;
            }
            // if only one is missing from the map, push it after.
            else if (aIndex === undefined) {
                position = 1;
            }
            else if (bIndex === undefined) {
                position = -1;
            }
            else {
                // if both are in the map, compare by the known order.
                position = aIndex - bIndex;
            }
            return position;
        });
    }
    return result;
}
